<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">CLASS</label>
            <label class="breadcrumb">Class List/Create Class</label>
        </div>
        <label class="page-title">CREATE CLASS</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>BANNER IMAGE <span class="red">*  (375 x 214px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="files_name">{{files_name}}</span>
                            <span v-else class="ellipsis">No Choosen Files</span>
                            <input type="file" value="" @change="onChangeFile($event, 'files', '')" class="form-control hidden-input" accept="image/*" required>
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>CLASS TITLE <span class="red">*</span></label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                   

                    <div class="form-group mb-45">
                        <label>CLASS ICON <span class="red">*  (50 x 48px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD CLASS ICON</button>
                            <span class="ellipsis" v-if="logo.name">{{logo.name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile($event, 'logo', '')" class="form-control hidden-input" accept="image/*" required>
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.description" required></textarea>
                    </div>
                    <div class="form-group mb-45">
                        <label>CANCELLATION POLICY <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.cancellationPolicy" required></textarea>
                    </div>
                    <div class="form-group mb-45">
                        <label>PREPARE <span class="red">*</span> </label>
                        <textarea type="text" class="form-control" v-model="form.prepare" required></textarea>
                    </div>
                    <div class="form-group mb-45">
                        <label class="mb-2">SPORT CLASS TYPE</label>
                        <multiselect
                            v-model="form.sportType"
                            placeholder="SELECT SPORT CLASS TYPE"
                            label="name" track-by="id"
                            :options="options"
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true"
                            :show-no-results="true"
                            @search-change="getSportClassTypeList">
                        </multiselect>
                    </div>
                    <div class="form-group mb-5">
                        <label>PRICE  <span class="red">*</span> </label>
                        <div class="input-group back">
                            <span class="back">CREDIT</span>
                            <input type="text" class="form-control" v-model="form.price" @keyup="formatPrice(form.price)" required>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-12 col-md-10 col-lg-8 col-xl-7">
                            <div class="form-group mb-5">
                                <label>DURATION  <span class="red">*</span> </label>
                                <div class="input-group back">
                                    <input type="number" class="form-control" min="1" v-model="form.duration" style="padding-right: 60px;" required>
                                    <span class="back">minutes</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-6 col-sm-4 mb-2">
                            <div class="form-group mb-5">
                                <label>LOYALTY POINTS  <span class="red">*</span> </label>
                                <div class="input-group back">
                                    <input min="0" type="number" class="form-control" v-model="form.rewardPoints" required>
                                    <span class="back">pts</span>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                         <div class="form-group mb-5">
                        <label class="mb-2">PRIORITY <span class="red">*</span></label>
                        <div class="row">
                            <div class="col-6 col-sm-4 mb-2">
                                <multiselect v-model="form.priority" :options="priority_options" :searchable="false" :close-on-select="true" :show-labels="false" placeholder="Choose Priority"></multiselect>
                            </div>
                        </div>
                    </div>
             
                  

                    <div class="">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'class', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>

    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Multiselect from 'vue-multiselect'
    import debounce from 'debounce'

    export default {
        name: '',
        components: {
            Multiselect
        },
        computed: mapState({
            user: 'user',
            isAuthenticated: 'isAuthenticated',
            files_name() {
                // return this.files.map(file => file.name).join(', ');
                return this.files.name;
            }
        }),
        data (){
            return {
                is_loading: false,
                hour: '',
                hour_options: ['00', '01', '02', '03', '04', '05'],
                minute: '',
                minute_options: ['00', '05', '10', '15', '20', '25', '30', '35', '40', '45', '50', '55'],
                files: '',
                logo: '',
                fontLogo: '',
                fontLogoColor: '',
                options: [],
                load_class: false,
                isLoading: false,
                priority_options: [1,2,3,4,5],
                form: {
                    name: '',
                    subTitle: '',
                    description: '',
                    duration: '',
                    rewardPoints: '',
                    cancellationPolicy: '',
                    prepare: '',
                    price: '',
                    sportType: '',
                    priority: '',
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getSportClassTypeList()
              this.getListClass()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.checkAuthority()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.checkAuthority()
                    }
                }
            },
            getListClass( page = null, sortColumn = null, sortType = null){
              
                this.$http.get(process.env.API_URL + 'sports-class', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType}}).then(response => {

                    var status = response.data.status
                    if(status){
                        var data = response.data.data.data
                        for(var i = 0; i < data.length; i++){
                            this.priority_options.push(i + 2)
                        }
                    }
                }, response => {})
            },
            checkAuthority(){
                if(this.profile.isOperator){
                    this.$toastr('error', "You don't have authorization to access this page!")
                    this.$router.push({ name: 'class', params: {} })
                }
            },
            onChangeFile(event, attribute, defaultValue) {
                var files = event.target.files || event.dataTransfer.files;
                const MAX_SIZE_FILE = 2*1024*1024
                if (files[0].size > MAX_SIZE_FILE) {
                  this.$toastr('error', 'file size max 2Mb')
                  return
                }
                if (files.length) {
                    this[attribute] = defaultValue
                    if (Array.isArray(defaultValue)){
                        for (let i = 0; i < files.length; i++){
                            this[attribute][i] = files[i]
                        }
                    } else {
                        this[attribute] = files[0]
                    }
                }
            },
            doCreate(){
               
                var formData = new FormData();
                formData.append('name', this.form.name)
                formData.append('subTitle', this.form.subTitle)
                formData.append('description', this.form.description)
                formData.append('cancellationPolicy', this.form.cancellationPolicy)
                formData.append('prepare', this.form.prepare)
                formData.append('duration', this.form.duration)
                formData.append('price', parseInt(this.form.price.toString().replace(/\./g,'')))
                formData.append('rewardPoints', this.form.rewardPoints)
                formData.append('sport_type', this.form.sportType ? this.form.sportType.map(e => e.id) : "")
                formData.append('images', this.files)
                formData.append('logo', this.logo)
                formData.append('priority', this.form.priority)
              
                
                if(this.form.priority === ""){
                       this.is_loading = false
                    this.$toastr('error', 'Please select a priority')
                    return
                }else {
                     this.is_loading = true
                    this.$http.post(process.env.API_URL + 'sports-class', formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'class', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
                }

               
            },
            formatPrice(value){
                this.form.price = this.formatNumber(value)
            },
            formatNumber(value){
                let temp = value.toString().replace(/\./g,'')
                let result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
            getSportClassTypeList: debounce(function(query) {
              this.load_class = true
                this.$http.get(process.env.API_URL + 'sport-class-type/all', {params: {'keyword': query}}).then(response => {
                    this.load_class = false
                    var status = response.data.status
                    if(status){
                        this.options = response.data.data
                    }
                }, response => {})
            }, 250),
        }
    }
</script>



