<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">USER</label>
            <label class="breadcrumb">User List/Edit User</label>
        </div>
        <label class="page-title">EDIT USER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_load_data">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <div class="form-group mb-4">
                        <label>NAME  <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'specialization', params: {id: id} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>

                    <div class="loading">

                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: 'edit-specialization',
        props: ['id'],
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},

                is_member: false,
                is_can_edit: false,
                is_load_data: false,
                is_loading: false,

                data: {},
                form: {
                    name: ''
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailSpecialization()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getDetailSpecialization() {
                this.is_load_data = true
                this.$http.get(process.env.API_URL + 'teacher-specialization/' + this.id).then(response => {
                    this.is_load_data = false
                    var status = response.data.status
                 
                    if(status){
                        const data = response.data.data
                        this.form.name = data.name
                    }
                }, response => {})
            },
            doUpdate(){
                this.is_loading = true
                this.$http.post(process.env.API_URL + 'teacher-specialization/update/' + this.id, this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(this.profile.id == this.data.id){
                            this.$parent.profile = response.data.data
                        }
                        this.$router.push({ name: 'specialization', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
    .radio-group input[type=radio] {
        position: absolute;
        visibility: hidden;
        display: none;
    }

    .radio-group label {
        color: #ef3b29;
        display: inline-block;
        cursor: pointer;
        font-weight: bold;
        padding: 9px 20px;
        width: 100%;
        border: solid 1px #ef3b29;
    }
    .radio-group label::before{
        content: none;
    }
    .radio-group label::after{
        content: none;
    }

    .radio-group input[type=radio]:checked + label {
        color: #fff;
        background: #ef3b29;
        border: solid 1px #ef3b29;
    }

    .radio-group label + input[type=radio] + label{
        border: solid 1px #ef3b29;
    }

    .radio-group {
        overflow: hidden;
    }
</style>



