<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">LOCATION</label>
            <label class="breadcrumb">Location List</label>
        </div>

        <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_location', params: {} }" class="btn btn-main inverse small">+ CREATE LOCATION</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th @click="doSortTable('name')">NAME</th>
                                <th>STATUS</th>
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="7" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="7">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" :key="index" v-else>
                                <td>
                                    <span>{{ item.location_name }}</span>
                                </td>

                                <td>
                  
                                    <span class="pointer" @click="showEditConfirmation(item, index)">{{ item.isActive === 0 ? "Inactive" : "Active" }}</span>
                  
                                </td>
                                <td>
                                    <router-link :to="{ name: 'edit_location', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                    <button type="button" class="btn btn-main inverse small mt-1 mb-1" @click="showDeleteConfirmation(item, index)">DELETE</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deleteLocation()">
            <div slot="header">DELETE LOCATION</div>
            <div slot="body">
                are you sure you want to delete this location? this action can not be reverted
            </div>
        </confirmation>
            <confirmationupdate
      v-if="update_confirmation"
      @close="update_confirmation = false"
      @confirm="doUpdate()"
    >
      <div slot="header">STATUS</div>
      <div slot="body">
         
        <div class="mt-2">
            <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isActive"
                      id="id-inactive"
                      value="0"
                    />
                    <label class="form-check-label" for="id-inactive">
                      Inactive
                    </label>
                  </div>
                  <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isActive"
                      id="id-active"
                      value="1"
                    />
                    <label class="form-check-label" for="id-active">
                      Active
                    </label>
                  </div>
                </div>
      </div>
    </confirmationupdate>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Paginate from 'vuejs-paginate'
    import debounce from 'debounce'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
    import Confirmation from '@/components/Confirmation'
    import ConfirmationUpdate from "@/components/ConfirmationUpdate";

    export default {
        name: 'location-list',
        components: {
            'paginate': Paginate,
            'confirmation': Confirmation,
             confirmationupdate: ConfirmationUpdate
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
         watch: {
        update_confirmation: function() {
            if(this.update_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
        },
        data (){
            return {
                profile: {},
                keyword: '',
                sort_column: '', // * fullName * role * email * phoneNumber *
                sort_type: '',
                is_loading: false,
                data: [],
                last_page: 0,
                current_page: 0,
                delete_confirmation: false,
                  update_confirmation: false,
                data_temp: {},
                index_temp: '',
                arrow: arrow,
                form: {
                    location_name: '',
                    isActive: 0
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListLocation(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            callbackPaginate: function(pageNum) {
                this.getListLocation(true, pageNum)
            },
            search: debounce(function(){
                this.getListLocation(false)
            },250),
 
            getListLocation(loading, page = null){
                this.is_loading = loading

                this.$http.get(process.env.API_URL + 'location', {params: {'keyword': this.keyword, 'page': page, 'limit' : 10 }}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
            showDeleteConfirmation(data, index){
                this.data_temp = data
                this.index_temp = index
                this.delete_confirmation = true
            },
             showEditConfirmation(data, index) {
                this.form.isActive = data.isActive
                this.form.location_name = data.location_name
                this.data_temp = data;
                this.index_temp = index;
                this.update_confirmation = true;
            },
             doUpdate() {
                 var formData = new FormData();
                formData.append('isActive', parseInt(this.form.isActive))
                formData.append('location_name', this.form.location_name)
      this.$http
        .post(process.env.API_URL + "location/update/" + this.data_temp.id, formData)
        .then(
          response => {
              this.update_confirmation = false;
              document.documentElement.style.overflow = 'auto'
            var status = response.data.status;
            if (status) {
            
              this.getListLocation(true);
              this.$toastr("success", response.data.message);
            }else {
              this.$toastr("error", response.data.message);
            }
          },
          response => {}
        );
    },
            deleteLocation() {
              this.$http.post(process.env.API_URL + 'location/delete/' + this.data_temp.id).then(response => {
                    this.delete_confirmation = false
                    document.documentElement.style.overflow = 'auto'
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
 .pointer {
  cursor: pointer;
  color: #00A6ED;
}
</style>



