<template>
<div class="">

  

    <div class="row">
        <div class="col-12 col-md-12 col-lg-8">
            <div class="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                           
                            <th>REMAINING CREDIT</th>
                            <th></th>
                            <th>ACTION</th>
                        </tr>
                    </thead>
                    <tbody v-if="is_loading">
                        <tr>
                            <td colspan="4" class="text-center">
                                <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                            </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr class="text-center" v-if="data.length <= 0">
                            <td colspan="4">
                                <span >No matching records found</span>
                            </td>
                        </tr>
                        <tr v-for="(item, index) in data" :key="item.id" v-else>
                         
                            <td> {{ item.credit }} </td>
                            <td></td>
                            <td></td>
    
                        </tr>
                    </tbody>
                    <tfoot v-if="data.length > 0">
                    <tr>
                    <td>
                       
                        <div class="title-hover"> TOTAL {{remaining_credit}}
                            <span class="tooltiptext">Current Credit</span>
                        </div>
                      
                       
                    </td>
                    <td>
                    
                    </td>
                    <td>
                        <button type="button" class="btn btn-main inverse small" @click="showFormAll()">EDIT</button>
                    </td>
                    </tr>
                </tfoot>
                </table>
            </div>

             <div class="">
                <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                    <paginate
                        :page-count="last_page"
                        :click-handler="callbackPaginate"
                        :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :container-class="'pagination'"
                        :page-class="'page-item'"
                        :page-link-class="'page-link'"
                        :prev-class="'page-item'"
                        :prev-link-class="'page-link'"
                        :next-class="'page-item'"
                        :next-link-class="'page-link'"
                        :page-range="5"
                        :margin-pages="4"
                        ref="paginate">
                    </paginate>
                </nav>
              </div>
        </div>
        
         <form-modal v-if="show_form_all" @close="doCloseForm()">
            <div slot="header">EDIT ACTIVE CREDIT</div>
            <div slot="body">
                <form @submit.prevent="doEditAll()">
                    <div class="modal-body">

                        <div class="row justify-content-center">
         
                            <div class="col-12 col-md-12">
                                <div class="form-group mb-5">
                                    <label class="mb-2">EXPIRATION DATE</label>
                                    <div class="input-group front back">
                                        <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                        <span class="back icon" v-if="date !== ''">
                                            <a href="#deleteDate" @click.prevent="date = ''">
                                                <i class="fa fa-times" aria-hidden="true"></i>
                                            </a>
                                        </span>
                                    </div>
                                    <datetime type="date" v-model="date" format="dd MMMM yyyy" value-zone="Asia/Jakarta" :input-class="{'form-control datetime front': true, 'error': has_error}" class="theme light" :auto="true" :week-start="7" required></datetime>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="modal-footer">
                        <button type="submit" class="btn btn-main small" :class="{'disabled': save_data}">
                            SAVE
                            <span class="btn-loading" v-if="save_data"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                        </button>
                        <button type="button" class="btn btn-main inverse small" @click="doCloseForm()">
                            CANCEL
                        </button>
                    </div>
                </form>
            </div>
        </form-modal>
    </div>
</div>
</template>

<script>
    import { Datetime } from 'vue-datetime'
    import Form from '@/components/Form'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
    import Paginate from 'vuejs-paginate'

    export default {
        name: 'active-package',
        props: ['id'],
        components: {
            'form-modal': Form,
            'datetime': Datetime,
            'paginate': Paginate
        },
        data () {
            return {
                is_loading:false,
                data: [],
                session_date: '',
                has_error: false,
                show_form: false,
                show_form_all: false,
                item_temp: {},
                index_temp: '',
                save_data: false,
                last_page: 0,
                current_page: 0,
                 arrow: arrow,
                 remaining_credit: '',
                 date: '',
            }
        },
        mounted(){
            this.request()
        },
        methods: {
              callbackPaginate: function(pageNum) {
                this.request(pageNum)
            },
            request(page = null){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'member/remaining-credit', {params: {'memberID': this.id, 'page' : page}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.remaining_credit = response.data.remaining_credit
                        this.date = response.data.expired
                        this.data = response.data.data.data
                          this.last_page = response.data.data.last_page ? response.data.data.last_page : 0
                        this.current_page = response.data.data.current_page ? response.data.data.current_page : 0
                    }
                }, response => {})
            },
             showFormAll(){
                this.show_form_all = true
            },
            doCloseForm(){
                this.show_form = false
                this.show_form_all = false
            },

             doEditAll(){
                this.save_data = true
                var moment = require('moment')
                var form = {
                    
                    expired: moment(this.date).format('YYYY-MM-DD')
                }
                    this.$http.post(process.env.API_URL + 'member-package/update-all-credit'+ '?memberID='+ this.id, form).then(response => {
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                       this.request()
                        this.save_data = false
                        this.show_form_all = false
                    }
                }, response => {})
                
               
            },
        }
    }
</script>

<style lang="css" scoped>
.title-hover{
      display: inline-block;
      position: relative;
    cursor: pointer;
    color: #ef3b29;
    font-size: 16px;
    letter-spacing: 1.43px;
    
}

/* Tooltip text */
.title-hover .tooltiptext {
    font-size: 12px;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
  visibility: hidden;
  width: 120px;
  background-color: #606060;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.title-hover:hover .tooltiptext {
  visibility: visible;
}

.title-hover .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #606060 transparent transparent transparent;
}
</style>



