<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">BANNER</label>
            <label class="breadcrumb">Banner List</label>
        </div>

        <div class="row justify-content-between align-items-center mb-4">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_banner', params: {} }" class="btn btn-main inverse small" :class="{'disabled': profile.isOperator}">+ CREATE BANNER</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-11 col-sm-11 col-md-11 col-lg-10 col-xl-10">
                <div class="row" v-if="is_loading">
                    <div class="col-10 col-sm-11">
                        <div class="card loading mb-4">
                            <i class="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
                        </div>
                    </div>
                </div>

                <div class="row mb-4 align-items-center" v-else v-for="(item, index) in data" :key="index">
                    <div class="col-10 col-sm-11">
                        <div class="card">
                            <div class="action-button">
                                <router-link :to="{ name: 'edit_banner', params: {id: item.id} }" class="mr-2 font-red">Edit</router-link>
                                <a href="#" class="font-red" @click.prevent="showDeleteConfirmation(item, index)">Delete</a>
                            </div>
                            <div class="row no-gutters">
                                <div class="col-md-4 card-image" :style="{ backgroundImage: 'url(' +item.imageUrl + ')' }"></div>
                                <div class="col-md-8">
                                    <div class="card-body">
                                        <h5 class="card-title">{{ item.title }}</h5>
                                        <div class="red font-bold font-14 mb-2">Description</div>
                                        <div class="desc"> {{ item.description }} </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-1 text-center">
                        <div class="">
                            <div class="mb-3">
                                <a href="#" @click.prevent="changeOrder(true, item, index, item.priority)" :class="{'disabled': item.priority == 1 || keyword !== ''}">
                                    <i class="fa fa-arrow-up fa-2x" aria-hidden="true"></i>
                                </a>
                            </div>
                            <div>
                                <span class="font-bold underline"> &nbsp; {{ item.priority }} &nbsp; </span>
                            </div>
                            <div class="mt-3">
                                <a href="#" @click.prevent="changeOrder(false, item, index, item.priority)" :class="{'disabled': item.priority == data.length || keyword !== ''}">
                                    <i class="fa fa-arrow-down fa-2x" aria-hidden="true"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>

        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deleteBanner()">
            <div slot="header">DELETE BANNER</div>
            <div slot="body">
                are you sure you want to delete this banner? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import debounce from 'debounce'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'class-list',
        components: {
            'confirmation': Confirmation,
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                IMAGE_URL: process.env.IMAGE_URL,
                profile: {},
                keyword: '',
                is_loading: false,
                data: [],

                delete_confirmation: false,
                item_temp: {},
                index_temp: '',
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListBanner(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            search: debounce(function(){
                this.getListBanner(false)
            },250),
            getListBanner(loading){
                this.is_loading = loading
                this.$http.get(process.env.API_URL + 'banner', {params: {'keyword': this.keyword}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data.sort()
                    }
                }, response => {})
            },
            changeOrder(isUp, item, index, priority){
                var temp = {}
                var positionTarget = index
                var status = false

                if(this.keyword == ''){
                    if(isUp){
                        positionTarget = index - 1
                        if(index > 0){
                            status = true
                        }
                    } else {
                        positionTarget = index + 1
                        if(index < this.data.length - 1){
                            status = true
                        }
                    }

                    if(status){
                        this.$http.post(process.env.API_URL + 'banner/update/'+ item.id, {priority: positionTarget + 1}).then(response => {
                            var status = response.data.status
                            if(status){
                                temp = this.data[positionTarget]
                                this.$set(this.data, positionTarget, this.data[index])
                                this.$set(this.data, index, temp)
                                this.$toastr('success', response.data.message)
                                this.data[positionTarget].priority = response.data.data.priority
                                this.data[index].priority = index + 1

                            }
                        }, response => {})
                    }
                }
            },
            showDeleteConfirmation(item, index){
                this.item_temp = item
                this.index_temp = index
                this.delete_confirmation = true
            },
            deleteBanner(){
                this.$http.post(process.env.API_URL + 'banner/delete/' + this.item_temp.id).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    a{
        color: #ef3b29;
    }
    a:hover{
        opacity: .7;
    }
    a.disabled{
        color: #9b9b9b;
        cursor: not-allowed;
    }
    .action-button{
        position: absolute;
        top: 7px;
        right: 10px;
        z-index: 10;
    }
    .card-body{
        padding-top: 35px;
        padding-bottom: 35px;
    }
    .card.loading{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .card-image{
        background-image: url('https://via.placeholder.com/200');
    }
    table th,
    table td{
        padding: 4px 15px;
        vertical-align: top;
    }
    table th:first-child,
    table td:first-child{
        padding-left: 0;
    }
    table th:last-child,
    table td:last-child{
        padding-right: 0;
    }
    table thead th{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    table tbody td{
        font-size: 12px;
    }

    .underline{
        border-bottom: solid 1px #ef3b29;
        padding-bottom: 3px;
    }
</style>



