import Vue from 'vue'
import Vuex from 'vuex'
import vueAuthInstance from '../services/auth.js'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        isAuthenticated: vueAuthInstance.isAuthenticated(),
        user: {}
    },

    getters: {
        isAuthenticated() {
            return vueAuthInstance.isAuthenticated()
        },
        user(state) {
            return state.user
        }
    },

    mutations: {
        isAuthenticated(state, payload) {
            state.isAuthenticated = payload.isAuthenticated
        },
        user(state, payload) {
            state.user = payload
        }
    },

    actions: {
        login(context, payload) {
            payload = payload || {}
            return vueAuthInstance.login(payload.user, payload.requestOptions).then(function (response) {
                if (response.status === 200 && response.data.status) {
                    context.commit('isAuthenticated', {
                        isAuthenticated: vueAuthInstance.isAuthenticated()
                    })
                    localStorage.setItem("user", JSON.stringify(response.data.user))
                    context.commit('user', response.data.user)
                    window.location.reload()
                    return response
                }
                return response
            }).catch(error => {
                return error.response
            })
        },

        register(context, payload) {
            payload = payload || {}
            return vueAuthInstance.register(payload.user, payload.requestOptions).then(function (response) {
                context.commit('isAuthenticated', {
                    isAuthenticated: vueAuthInstance.isAuthenticated()
                })
                return response
            }).catch(error => {
                return error.data;
            })
        },

        logout(context, payload) {
            payload = payload || {}
            return vueAuthInstance.logout(payload.requestOptions).then(function () {
                context.commit('isAuthenticated', {
                    isAuthenticated: vueAuthInstance.isAuthenticated()
                })
                localStorage.removeItem("user")
                context.commit('user', {})
            })
        },

        authenticate(context, payload) {
            payload = payload || {}
            return vueAuthInstance.authenticate(payload.provider, payload.userData, payload.requestOptions).then(response => {
                context.commit('isAuthenticated', {
                    isAuthenticated: vueAuthInstance.isAuthenticated()
                })
            }, response => {

            })
        },

        setProfile(context, payload) {
            payload = payload || {};
            if (context.getters.isAuthenticated) {
                return Vue.axios.get(process.env.API_URL + 'access').then(response => {
                    if (response.status === 200 && response.data.status) {
                        context.commit('user', response.data.data)
                        return response
                    }
                    return response.response
                });
            } else {
                context.commit('user', {});
            }
        }

    }
})

export default store



