<template lang="html">
    <div>
        <div class="mb-4">
            <label class="page-header">SCHEDULE</label>
            <label class="breadcrumb">Schedule List/Session Detail</label>
        </div>

        <label class="page-title mr-4">SESSION DETAIL</label>
        <span v-if="!profile.isOperator" action-button>
            <!-- <button type="button" class="btn btn-main inverse small mr-2 mb-1" @click="show_form = true">Edit Session</button> -->
            <button type="button" class="btn btn-main inverse small mr-2 mb-1" @click="doDeleteSessionValidation" >Delete Session </button>
          
            <button  type="button" class="btn btn-main small mb-1" @click="cancel_confirmation = true" v-if="this.islistBookingList && !this.is_expired &&data.isCanceled === 0">Cancel Session </button>
            <button type="button" class="btn btn-main small mb-1"  v-if=" data.isCanceled === 1 ">Cancelled</button>
       
        </span>

        <div class="row mt-4 mb-5">
            <div class="col-12 col-lg-11">
                <div class="row">
                    <div class="col-12 col-md-12 col-lg-5 mb-3 text-center">
                        <img :src="data_image" alt="" class="img-fluid" v-if="!is_loading">
                        <i class="fa fa-spinner fa-pulse fa-2x fa-fw" v-else></i>
                    </div>
                    <div class="col-12 col-md-12 col-lg-7">
                        <h5 v-if="data.sportsClass" class="card-title">{{data.sportsClass.name}}</h5>
                        <div class="row">
                                    <div class="col-6">
                                        <label class="title">Description</label>
                                        <p>
                                    <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    <span v-else-if="data.sportsClass">
                                        {{ data.sportsClass.description }}
                                    </span>
                                     <span v-else-if="data.teacher">
                                        {{ data.teacher.about }}
                                    </span>
                                     <span v-else>
                                        -
                                    </span>
                                </p>
                                    </div>
                                    
                                </div>
                        <div class="row">
 
                            <div class="col-12 col-sm-10 col-md-10 col-lg-6">
                                <div class="row">
                                    <div class="col-6">
                                        <label class="title">Session Date</label>
                                        <p>
                                            <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                            <span v-else v-html="$options.filters.dateDay(data.start)"></span>
                                        </p>
                                    </div>
                                    <div class="col-6">
                                        <label class="title">Session Time</label>
                                        <p>
                                            <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                            <span v-else>{{data.start | timeFormatDate}}-{{data.endTime | timeFormatTime}}</span>
                                        </p>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-6">
                                        <label class="title">Duration</label>
                                        <p>
                                            <!-- <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span> -->
                                            <span v-if="data.sportsClass">{{ data.sportsClass.duration }} minutes </span>
                                             <span v-else-if="data.category === 'personal trainer'">{{ data.teacher.duration }} minutes </span>
                                               <span v-else>- </span>
                                        </p>

                                        <label v-if="data.category === 'class' || data.category === 'personal trainer'" class="title">Trainer</label>
                                        <p v-if="data.category === 'class' || data.category === 'personal trainer'">
                                            <span v-if="data.teacher">{{  data.teacher.firstName + ' ' + data.teacher.lastName }}</span>
                                            <span v-else>-</span>
                                        </p>
                                       
                                    </div>
                                    <div class="col-6">
                                        <label class="title">Slots Filled</label>
                                        <p>
                                            <span v-if="is_loading"><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                            <span v-else>{{data.slotTaken}}/{{data.quota}}
                                                <button type="button" class="btn btn-main small mt-1" style="min-width: 80px;" @click="show_edit_slot = true">EDIT SLOT</button>
                                            </span>
                                        </p>
                                    </div>
                                </div>
                                 <div class="row">
                                    <div class="col-6">
                                        <label class="title">Location</label>
                                        <p>
                                       
                                            <span v-if=data.location > {{data.location.location_name}} </span>
                                             <span v-else > -</span>
                                        </p>
                                    </div>
                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="tab">
            <ul class="mb-4">
                <router-link :to="{ name: 'booking_list', params: {} }" tag="li">
                    <a href="#">BOOKING LIST</a>
                </router-link>
                <router-link :to="{ name: 'waiting_list', params: {} }" tag="li">
                    <a href="#">WAITING LIST</a>
                </router-link>
            </ul>

            <router-view/>
        </div>

        <form-modal v-if="show_form" @close="show_form = false">
            <div slot="header">EDIT SESSION</div>
            <div slot="body">
                <form @submit.prevent="doUpdate()">
                    <div class="modal-body">
                        <div class="row justify-content-center">
                            <div class="col-12 col-lg-9">
                         
                                <div class="form-group mb-5">
                                    <label class="mb-2">SET DATE & TIME <span class="red">*</span></label>
                                    <div class="input-group front back">
                                        <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                        <span class="back icon" v-if="session_date !== ''">
                                            <a href="#deleteDate" @click.prevent="session_date = ''">
                                                <i class="fa fa-times" aria-hidden="true"></i>
                                            </a>
                                        </span>
                                    </div>
                                    <datetime type="datetime" v-model="session_date" format="dd MMMM yyyy HH:mm 'WIB'" value-zone="Asia/Jakarta" :input-class="{'form-control datetime front': true, 'error': has_error}" class="theme light" :auto="true" :week-start="7" required></datetime>
                                    <span class="font-error" v-if="has_error">
                                        <small> Session is already used by other class </small>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="modal-footer">
                        <button type="submit" class="btn btn-main small" :class="{'disabled': save_data}">
                            SAVE
                            <span class="btn-loading" v-if="save_data"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                        </button>
                        <button type="button" class="btn btn-main inverse small" @click="show_form = false; save_data = false;">
                            CANCEL
                        </button>
                    </div>
                </form>
            </div>
        </form-modal>

        <form-modal v-if="show_edit_slot" @close="doCloseEditSlot()">
            <div slot="header">EDIT SLOT</div>
            <div slot="body">
                <form @submit.prevent="doUpdateSlot()">
                    <div class="modal-body">
                        <div class="row justify-content-center">
                            <div class="col-12 col-lg-9">
                                <div class="form-group mb-5">
                                    <label class="mb-2">SLOT</label>
                                    <input type="text" class="form-control" v-model="edit_slot_value" required>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="modal-footer">
                        <button type="submit" class="btn btn-main small" :class="{'disabled': edit_slot}">
                            SAVE
                            <span class="btn-loading" v-if="edit_slot"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                        </button>
                        <button type="button" class="btn btn-main inverse small" @click="doCloseEditSlot()">
                            CANCEL
                        </button>
                    </div>
                </form>
            </div>
        </form-modal>

        <confirmation v-if="delete_confirmation && !profile.isOperator" @close="delete_confirmation = false" @confirm="deleteSession()">
            <div slot="header">DELETE SESSION</div>
            <div slot="body">
                are you sure you want to delete this session? this action can not be reverted
            </div>
        </confirmation>

        <confirmation v-if="cancel_confirmation && !profile.isOperator" @close="cancel_confirmation = false" @confirm="cancelSession()">
            <div slot="header">CANCEL SESSION</div>
            <div slot="body">
                are you sure you want to cancel this session? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Multiselect from 'vue-multiselect'
    import Confirmation from '@/components/Confirmation'
    import Form from '@/components/Form'

    export default {
        name: 'detail-user',
        props: ['id', 'date'],
        components: {
            Multiselect,
            'confirmation': Confirmation,
            'form-modal': Form,
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data(){
            return{
                IMAGE_URL: process.env.IMAGE_URL,
                profile: {},
                is_loading: false,
                delete_confirmation: false,
                cancel_confirmation: false,
                data: {},
                keyword: '',
                data_users: [],
                data_image: '',
                islistBookingList: false,
                selected_category: null,
                selected_category_id : null,
                selected_location : null,
                location_options: [],
                show_form: false,
                session_date: '',
                selected_class: null,
                class_options: [],
                show_edit_slot: false,
                edit_slot: false,
                edit_slot_value: '',
                has_error: false,
                save_data: false,
                is_expired : false,
                form: {
                    start: '',
                    sportsClassID: ''
                },
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailSchedule()
            this.getListClass()
            this.getBookingList()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getDetailSchedule(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'session/' + this.id).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                        if(this.data.category === 'personal trainer') {
                             this.data_image =  this.data.teacher.imageUrl
                          
                        }else {
                              this.data_image =  this.data.sportsClass.imageUrl
                        }
                      
                        this.selected_class = this.data.sportsClass
                        this.edit_slot_value = this.data.quota
                        var moment = require('moment')
                        this.session_date = moment(this.data.start).format("YYYY-MM-DD") + 'T' + moment(this.data.start).format("HH:mm:ss") + ".000+07:00" 
                        var current_date = moment().format("YYYY-MM-DD") + 'T' + moment().format("HH:mm:ss") + ".000+07:00"
                        if(current_date > this.session_date) {
                            this.is_expired = true
                        }else {
                            this.is_expired = false
                        }
       
                    }
                }, response => {})
            },
            getListClass(){
                this.$http.get(process.env.API_URL + 'sports-class').then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.class_options = response.data.data
                    }
                }, response => {})
            },
            getBookingList(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'session/booking-list/' + this.id).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(response.data.data.filter(value => value.status == 'Booked').length > 0) {
                            this.islistBookingList = true
                        }else {
                            this.islistBookingList = false
                        }

  
                      
                    }
                }, response => {})
            },
            deleteSession(){
                if(this.islistBookingList) {
                    this.$toastr('error', 'Sorry, this schedule cannot be deleted')
                    this.delete_confirmation = false
                }else {
                    this.$http.post(process.env.API_URL + 'session/delete/' + this.id).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'schedule', params: {date: this.date} })
                    }
                }, response => {})
                }
               
            },
            cancelSession(){
                this.$http.post(process.env.API_URL + 'session/cancel-all',{sessionID: this.id}).then(response => {
                    this.delete_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'schedule', params: {date: this.date} })
                    }
                }, response => {})
            },
            doUpdate(){
                if(this.selected_class == null){
                    this.$toastr('error', 'Class required')
                    return
                }

                var moment = require('moment')
                var date = moment(this.session_date).format('DD-MM-YYYY')
                this.save_data = true
                this.form.sportsClassID = this.selected_class.id
                this.form.start = moment(this.session_date).format('YYYY-MM-DD HH:mm:ss')
                this.$http.post(process.env.API_URL + 'session/update/' + this.id, this.form).then(response => {
                    var status = response.data.status
                    if(status){
                        this.selected_class = null
                        this.form.sportsClassID = ''
                        this.form.start = ''
                        this.session_date = ''
                        this.save_data = false
                        this.show_form = false
                        this.$toastr('success', response.data.message)
                        this.getDetailSchedule()
                    } else {
                        this.save_data = false
                    }
                }, response => {})
            },
            doDeleteSessionValidation(){
                this.delete_confirmation = true
                this.getBookingList()
            },
            doCloseEditSlot(){
                this.show_edit_slot = false
            },
            doUpdateSlot(){
                this.edit_slot = true
                this.$http.post(process.env.API_URL + 'session/update/' + this.id, {quota: this.edit_slot_value}).then(response => {
                    this.edit_slot = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.data.quota = this.edit_slot_value
                        this.show_edit_slot = false
                    }
                }, response => {})
            },

            subtrackSlotTaken(){
                this.data.slotTaken -= 1
            },
            addSlotTaken(){
                this.data.slotTaken += 1
            },
            getMaximumAdditionBookedMember(){
                return this.data.quota - this.data.slotTaken
            },
        }
    }
</script>

<style lang="css" scoped>
    label.title{
        font-size: 14px;
        color: #ef3b29;
        font-family: 'gothambold';
    }
    p{
        color: #000;
    }
    .tab ul{
        padding-left: 0;
        list-style: none;
    }
    .tab ul li{
        display: inline-block;
    }
    .tab ul li{
        padding: 5px 25px;
    }
    .tab ul li a{
        color: #ef3b29;
        font-size: 16px;
    }
    .tab ul li.active{
        background-color: #ef3b29;
        border-radius: 25px;
        padding: 5px 25px;
    }
    .tab ul li.active a{
        color: #fff;
    }
    .tab ul li a:not(.active):hover{
        opacity: .7;
    }
    .tab ul li a.active:hover{
        box-shadow: 0 0 3px 1px #ef3b29;
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }
</style>



