<template>
    <div>
        <div class="mb-4">
            <label class="page-header">DASHBOARD</label>
            <label class="breadcrumb">Booking Summary</label>
        </div>

        <label class="page-title">BOOKING SUMMARY</label>
        <div class="row align-items-center ml-1 justify-content-between mr-5">
        <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3 no-padding-left">
            <div class="form-group  ">
                <label class="mb-2">CATEGORY FILTER</label>
                  <multiselect v-model="filter_category" :options="category_options_filter" :searchable="true"    :hide-selected="true" :show-no-results="true" track-by="id" label="name" :close-on-select="true" :show-labels="false" placeholder="SELECT CATEGORY" @select="onChangeFilter" ></multiselect>
            </div>
            
        </div>    
        <div class="form-group  ">
            <div class="row align-items-center ml-1">
     
            <div>
                <label class="mr-2"> CURRENT DATE</label>
                <div  @click="openFilter()">
                <date-range-picker
                    disabled
                    ref="picker"
                    opens="center"
                    :ranges ='false'
                    :locale-data="locale"
                    v-model="dateRange"
                    @update="update"
                    
                    >
   
                </date-range-picker>
                </div>
            </div>
    
                <div class="ml-2 mr-2 mt-4"  v-if="compared">
                    -
                </div>
                <div v-if="compared">
                    <label class="mr-2"> COMPARE DATE</label>
                    <div  @click="openFilter()" >
                        <date-range-picker
                        disabled
                            ref="picker"
                            opens="center"
                                :ranges ='false'
                        :locale-data="locale"
                            v-model="dateRange2"
                            @update="update2">
        
                        </date-range-picker>
                    </div>
    
            </div>
            </div>
        </div>
        </div>
    
   
        <div class="row ml-2">
            <div class="mr-4">
                <label class="font-10 mb-2">Attended</label>
                <div class="panel-body">  

                <label class="switch">
                <input type="checkbox" :value="isattended"  v-model="isattended" @click="HideAttended">
                <div class="slider round"></div>
                </label>
                </div>
            </div>
            <div class=" mr-4">
                <label class="font-10 mb-2">No Show</label>
                <div class="panel-body">  

                <label class="switch">
                    <input type="checkbox" :value="isnoshow"  v-model="isnoshow" @click="HideNoShow">
                    <div class="slider round"></div>
                </label>
                </div>
            </div>
            <div class="mr-4">
                <label class="font-10 mb-2">Late Cancel</label>
                <div class="panel-body">  

                <label class="switch">
                    <input type="checkbox" :value="iscancel"  v-model="iscancel" @click="HideCancel">
                    <div class="slider round"></div>
                </label>
                </div>
            </div>
            <div class="mr-4">
                <label class="font-10 mb-2">Booking</label>
                <div class="panel-body">  

                <label class="switch">
                    <input type="checkbox" :value="isbooking"  v-model="isbooking" @click="HideBooking">
                    <div class="slider round"></div>
                </label>
                </div>
            </div>
        </div>

        <div id="chart">
            <apexchart ref="apexChart" type="line" height="350" :options="chartOptions" :series="series"></apexchart>
        </div>
    
    

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th>DATE</th>
                                <th>CATEGORY</th>
                                <th>CLASS</th>
                                <th>START TIME</th>
                                <th>FULL NAME</th>
                                <th>STATUS</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="5" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="5">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" :key="index" v-else>
                                <td> {{ item.session.start | dateFormatFull }} </td>
                                <td>{{item.session.category}}</td>
                                <td v-if="item.session.category === 'personal trainer'"> {{ item.session.teacher.firstName }} {{ item.session.teacher.lastName }} </td>
                             
                                <td v-else>{{ item.session.sportsClass.name }} </td>
                                <td> {{ item.session.start | timeFormatDate }}  </td>
                                <td>
                                    <span v-if="item.memberPackage.member && item.memberPackage.member.user">
                                        <router-link :to="{ name: 'detail_user', params: {id: item.memberPackage.member.user.id} }" class='link'>
                                            {{ item.memberPackage.member.user.firstName + ' ' + item.memberPackage.member.user.lastName }}
                                        </router-link>
                                    </span>
                                </td>
                                <td> {{ item.status }} </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate_">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
        <form-modal
            v-if="update_confirmation"
            @close="update_confirmation = false"
            @confirm="doUpdate()">

            <div slot="header">FILTER DATA</div>
            <div slot="body">
            <div class="text-left-center">
                <div class="row-filter">
                <div class="column-filter">
                    <div class="column">
                        <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisWeek == 0}" @click="doFilter(1, 0, 0,0,0,0)">THIS WEEK</button>
                        <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisTwoWeeks == 0}" @click="doFilter(0, 0, 0,1,0,0,0)">THIS TWO WEEK</button>
                        <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisMonth == 0}" @click="doFilter(0, 1, 0,0,0,0)">THIS MONTH</button>
                        <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisThreeMonth == 0}" @click="doFilter(0, 0, 0,0,1,0)">LAST THREE MONTH</button>
                            <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisSixMonth == 0}" @click="doFilter(0, 0, 0,0,0,1)">LAST SIX MONTH</button>
                        <button type="button" class="btn-full btn-filter  mb-2 mr-2" :class="{'inverse': thisYear == 0}" @click="doFilter(0, 0, 1,0,0,0)">THIS YEAR</button>
                        
                    </div>
        </div>

        <div class="column-filter ml-4">

            <label class=" mb-2">Compare</label>
            <div class="panel-body">  

            <label class="switch">
                <input type="checkbox" :value="compared"  v-model="compared" @click="toggleCheckbox">
                <div class="slider round"></div>
            </label>
            </div>

            <label class=" mb-2">Current</label>
        <div class="">
                    <div >
                        <date-range-picker
                            ref="picker"
                            opens="center"
                                :ranges ='false'
                        :locale-data="locale"
                            v-model="dateRange"
                            @update="update">
        
                        </date-range-picker>
                    </div>
            </div>

            <div v-if="compared">
            <label class=" mb-2">Compare</label>
                <div class="">
                    <div >
                        <date-range-picker
                            
                            ref="picker"
                            opens="center"
                                :ranges ='false'
                        :locale-data="locale"
                            v-model="dateRange2"
                            @update="update2">
        
                        </date-range-picker>
                    </div>
            </div>
            </div>
        </div>
        </div>
        
            
        </div>
            <div class="btn-apply">
                <button :disabled="compared && (range1 !== range2)" type="submit" @click="doUpdate()" class="btn btn-main small" :class=" compared && (is_loading ||range1 !== range2) ? 'disabled-filter' : '' ">
                                    APPLY
                <span class="btn-loading" v-if="is_loading"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                </button>
                            
            </div>
            </div>
        </form-modal>
    </div>
</template>

<script>
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
import ConfirmationUpdate from "@/components/ConfirmationUpdate"
import Form from '@/components/Form'
import VueApexCharts from 'vue-apexcharts'
import { Datetime } from 'vue-datetime'
import Multiselect from 'vue-multiselect'
import { mixin as onClickOutside } from 'vue-on-click-outside'
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'
import Paginate from 'vuejs-paginate'
import { mapState } from 'vuex'


    export default {
        name: 'bookings',
        components: {
            'datetime': Datetime,
            'paginate': Paginate,
            DateRangePicker,
             Multiselect,
            apexchart: VueApexCharts,
            confirmationupdate: ConfirmationUpdate,
            'form-modal': Form,
        },
        mixins: [onClickOutside],
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        watch: {
             update_confirmation: function() {
                if(this.update_confirmation){
                    document.documentElement.style.overflow = 'hidden'
                return
                }
                document.documentElement.style.overflow = 'auto'
            },
          
        },
        data() {
            return {
                profile: {},
                show_filter_dropdown: false,
                update_confirmation: false,
                thisWeek: 1,
                thisMonth: 0,
                thisYear: 0,
                thisTwoWeeks:0,
                thisThreeMonth:0,
                thisSixMonth:0,
                sportsClassIDs: [],
                is_loading: false,
                data: [],
                last_page: '',
                current_page: 5,
                classes: [],
                load_statistic: false,
                total_bookings: '',
                attended: '',
                no_show: '',
                cancel: '',
                arrow: arrow,
                 filter_category: { id: "", name:"All" },
                filter_category_id : null,
                category_options_filter: [{ id: "", name:"All" },{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal Trainer"} , { id: "wellness",name: "Wellness"},{ id: "recovery",name: "Recovery"}],
                dateRange: {
                startDate:"",
                endDate: ""
                },
                 dateRange2: {
                startDate: "",
                endDate: ""
                },
                locale: {
                direction: 'ltr', //direction of text
                format: 'yyyy-mm-dd',
                separator: ' - ', //separator between the two ranges
                applyLabel: 'Apply',
                cancelLabel: 'Cancel',
                weekLabel: 'W',
                customRangeLabel: 'Custom Range',
                // firstDay: 1 
                },
                series : [],
                chartOptions : {},
                attended : [],
                noshow : [],
                cancel : [],
                booking :[],
                attended_2 : [],
                noshow_2 : [],
                cancel_2 : [],
                booking_2 : [],
                compared: false,
                range1: 0,
                range2: 0,
                isattended: true,
                iscancel: true,
                isbooking: true,
                isnoshow: true,
                formatted_date : [],
                status_booking: [],
            }
        },
        mounted(){
            this.getAuthUser()
            this.getStatistic()
            this.getClassList()
            this.getBookingList()
      
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getClassList(){
                this.$http.get(process.env.API_URL + 'sports-class', {params: {'keyword': this.keyword}}).then(response => {
                    var status = response.data.status
                    if(status){
                        this.classes = response.data.data.data
                    }
                }, response => {})
            },
            openFilter(){
               this.update_confirmation = true
            },
            toggleCustom() {
                this.customdate = !this.customdate
            },
             toggleCheckbox() {
                this.compared = !this.compared
               
            },
            callbackPaginate: function(pageNum) {
                this.getBookingList(pageNum)
            },
            HideAttended: function() {
                this.$refs.apexChart.toggleSeries('Attended')
                if (this.compared) {
                      this.$refs.apexChart.toggleSeries('Attended (comparison)')
              
                }
               this.isattended = !this.isattended
                   
                this.getBookingList()
            },
            HideNoShow: function() {
                this.$refs.apexChart.toggleSeries('No show')
                if (this.compared) {
                      this.$refs.apexChart.toggleSeries('No show (comparison)')
              
                }
               this.isnoshow = !this.isnoshow
              
                   this.getBookingList()
            },
            HideCancel: function() {
                this.$refs.apexChart.toggleSeries('Late Cancel')
                if (this.compared) {
                      this.$refs.apexChart.toggleSeries('Late Cancel (comparison)')
              
                }
                  this.iscancel = !this.iscancel
                   this.getBookingList()
            },
            HideBooking: function() {
                this.$refs.apexChart.toggleSeries('Booking')
                if (this.compared) {
                      this.$refs.apexChart.toggleSeries('Booking (comparison)')
              
                }
                  this.isbooking = !this.isbooking
                   this.getBookingList()
            },
            doFilter(thisWeek, thisMonth, thisYear, thisTwoWeeks,thisThreeMonth,thisSixMonth){
                this.thisWeek = thisWeek
                this.thisMonth = thisMonth
                this.thisYear = thisYear
                this.thisTwoWeeks = thisTwoWeeks
                this.thisThreeMonth = thisThreeMonth
                this.thisSixMonth = thisSixMonth
                
                if (thisWeek === 1) {
                    this.thisWeek = thisWeek
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0
                
                } else if(thisMonth === 1) {
                    this.thisMonth = thisMonth
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0
                    
                } else if(thisYear === 1) {
                    this.thisYear = thisYear
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0
        
                } else if(thisTwoWeeks === 1) {
                    this.thisTwoWeeks = thisTwoWeeks
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0

                } else if(thisThreeMonth === 1) {
                    this.thisThreeMonth = thisThreeMonth
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0

                } else if(thisSixMonth === 1){
                    this.thisSixMonth = thisSixMonth
                    this.dateRange.startDate = ""
                    this.dateRange.endDate = ""
                    this.dateRange2.startDate = ""
                    this.dateRange2.endDate = ""
                    this.range1 = 0
                    this.range2 = 0
                    
                } 
                this.getDate()
          
            },
            doUpdate(){
                this.update_confirmation = false
               
                this.getStatistic()
                this.getBookingList()
            },
         
             update(){
               const moment = require('moment')
                this.thisWeek = 0
                this.thisTwoWeeks = 0
                this.thisMonth = 0
                this.thisThreeMonth = 0
                this.thisSixMonth = 0
                this.thisYear = 0
                this.thisWeek2 = 0
                this.thisTwoWeeks2 = 0
                this.thisMonth2 = 0
                this.thisThreeMonth2 = 0
                this.thisSixMonth2 = 0
                this.thisYear2 = 0
                var start =moment(this.dateRange.startDate).format('YYYY-MM-DD')
                var end = moment(this.dateRange.endDate).format('YYYY-MM-DD')
                const date1 = new Date(start);
                const date2 = new Date(end);    
                const diffTime = Math.abs(date1 - date2);
                this.range1 = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
                
             
            },
             update2() {
                const moment = require('moment')
                this.thisWeek = 0
                this.thisTwoWeeks = 0
                this.thisMonth = 0
                this.thisThreeMonth = 0
                this.thisSixMonth = 0
                this.thisYear = 0
                this.thisWeek2 = 0
                this.thisTwoWeeks2 = 0
                this.thisMonth2 = 0
                this.thisThreeMonth2 = 0
                this.thisSixMonth2 = 0
                this.thisYear2 = 0
                var start =moment( this.dateRange2.startDate).format('YYYY-MM-DD')
                var end = moment( this.dateRange2.endDate).format('YYYY-MM-DD')
                const date1 = new Date(start);
                const date2 = new Date(end);    
                const diffTime = Math.abs(date1 - date2);
                this.range2 = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
             },
           
            onChangeFilter(){
                this.getStatistic()
                this.getBookingList()
            },
          
            getStatistic(){
                const moment = require('moment')
                this.filter_category_id = this.filter_category !== null  ?this.filter_category.id : ''
                this.dateRange.startDate = this.dateRange.startDate !== '' ? moment( this.dateRange.startDate).format('YYYY-MM-DD') : ''
                this.dateRange.endDate=  this.dateRange.endDate !== '' ? moment( this.dateRange.endDate).format('YYYY-MM-DD') : ''
                 this.dateRange2.startDate = this.dateRange2.startDate !== '' ? moment( this.dateRange2.startDate).format('YYYY-MM-DD') : ''
                this.dateRange2.endDate=  this.dateRange2.endDate !== '' ? moment( this.dateRange2.endDate).format('YYYY-MM-DD') : ''
                     this.isattended = true
                     this.iscancel = true
                     this.isbooking =true
                     this.isnoshow = true

                var params = {
                    thisWeek: this.thisWeek,
                    thisTwoWeeks : this.thisTwoWeeks,
                    thisMonth: this.thisMonth,
                    thisThreeMonth : this.thisThreeMonth,
                    thisSixMonth : this.thisSixMonth,
                    thisYear: this.thisYear,
                    start:  this.dateRange.startDate,
                    end:  this.dateRange.endDate,
                    thisWeek2: this.thisWeek,
                    thisTwoWeeks2 : this.thisTwoWeeks,
                    thisMonth2: this.thisMonth,
                    thisThreeMonth2 : this.thisThreeMonth,
                    thisSixMonth2 : this.thisSixMonth,
                    thisYear2: this.thisYear,
                    start2:  this.dateRange2.startDate,
                    end2:  this.dateRange2.endDate,
                    "category[]": this.filter_category_id ,
                    sportsClassIDs: this.sportsClassIDs,
                }
                this.load_statistic = true
                this.$http.get(process.env.API_URL + 'member-session/status-statistic', {params: params}).then(response => {
                    this.load_statistic = false
                    var status = response.data.status
                    if(status){
                        
                          this.attended = response.data.data.attended.count
                          this.booking = response.data.data.booking.count
                          this.cancel = response.data.data.cancel.count
                          this.noshow = response.data.data['no show'].count
                          this.attended_2 = response.data.data['attended 2'].count
                          this.booking_2 = response.data.data['booking 2'].count
                          this.cancel_2 = response.data.data['cancel 2'].count
                          this.noshow_2 = response.data.data['no show 2'].count
                    
                        if(this.thisThreeMonth === 1 || this.thisSixMonth === 1 || this.thisYear === 1){
                            this.formatted_date =  response.data.data.attended.date.map((element) => moment(element).locale("id").format('MMM YYYY')) 
                            var day = moment().format("DD")
                            this.dateRange.startDate = response.data.data.attended.date[0] 
                            this.dateRange.endDate = response.data.data.attended.date[response.data.data.attended.date.length - 1] + "-" + day
                            this.dateRange2.startDate = response.data.data['attended 2'].date[0]
                            this.dateRange2.endDate = response.data.data['attended 2'].date[response.data.data['attended 2'].date.length - 1]  + "-" + day
                        }else {
                            this.formatted_date =  response.data.data.attended.date.map((element) => moment(element).locale("id").format('ll')) 
                            this.dateRange.startDate = response.data.data.attended.date[0]
                            this.dateRange.endDate = response.data.data.attended.date[response.data.data.attended.date.length - 1]
                            this.dateRange2.startDate = response.data.data['attended 2'].date[0]
                            this.dateRange2.endDate = response.data.data['attended 2'].date[response.data.data['attended 2'].date.length - 1] 
                        }

                          if(this.compared === true) {
                                this.series = [
                                    {
                                    name: "Attended",
                                    color: "#3c6bdf",
                                    data: this.attended ? this.attended : []
                                },
                                {
                                    name: "Attended (comparison)",
                                    color: "rgba(60, 107, 223, 0.22)",
                                    
                                    data: this.attended_2 ? this.attended_2 : []
                                },
                             
                                {
                                    name: "No show",
                                    color: "#ffa200",
                                    data: this.noshow ? this.noshow : []
                                },
                                {
                                    name: "No show (comparison)",
                                    color: "rgba(255, 162, 0, 0.22)",
                                    data: this.noshow_2 ? this.noshow_2 : []
                                },
                               
                                {
                                    name: "Late Cancel",
                                    color: "#f02749",
                                    data: this.cancel ? this.cancel : []
                                },
                                {
                                    name: "Late Cancel (comparison)",
                                    color: "rgba(240, 39, 73, 0.22)",
                                    data: this.cancel_2 ? this.cancel_2 : []
                                },
                               
                                {
                                    name: "Booking",
                                    color: "#27f03e",
                                    data: this.booking ? this.booking : []
                                },
                                
                                 
            
                                {
                                    name: "Booking (comparison)",
                                    color: "rgba(39, 240, 62, 0.22)",
                                    data: this.booking_2 ? this.booking_2 : []
                                },
                                ]
                                this.chartOptions = {
                                    legend: {
                                    show:true,
                                    position: "bottom",
                                    horizontalAlign: "center",
                                 
                                    markers: {
                                        width: [12,0, 12, 0, 12,0,12,0],// hides first marker
                                    },
                                      customLegendItems: ["Attended","","No show", "","Late Cancel","", "Booking", ""],
                                   
                                    },
                                    chart: {
                            
                                        toolbar: {
                                            show: false
                                        },
                                    height: 350,
                                    type: 'line',
                                    zoom: {
                                        enabled: false
                                    }
                                    },
                                    dataLabels: {
                                    enabled: false
                                    },
                                    stroke: {
                                    curve: 'straight',
                                
                                    },
                                    title: {
                                
                                    align: 'left'
                                    },
                                    grid: {
                                    row: {
                                        colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                                        opacity: 0.5
                                    },
                                    },
                                    xaxis: {
                                    categories: this.formatted_date
                                    },
                                    tooltip: {
                                        y: {
                                            formatter: function (y, opts) {
                                           
                                            let names = opts.w.globals.seriesNames[opts.seriesIndex];
                                            let data1 = ""
                                            let data2 = ""
                                            let percent = ""

                                            if(y !== undefined){
                                           
                                                if(names === "Attended") {
                                                    data1 = opts.w.globals.seriesLog[opts.seriesIndex][opts.dataPointIndex]  ;
                                                    data2 = opts.w.globals.seriesLog[parseInt(opts.seriesIndex)+1][opts.dataPointIndex]  ;
                                               
                                                        if (data1===0 && data2 === 0) {
                                                            percent = 0
                                                        }else if(data2 === 0) {
                                                            let range = (data1 +1)  - (data2 +1)
                                                            let count = (range /(data2+1)) 
                                                            percent = count * 100
                                                       
                                                        }else {
                                                            let range = data1  - data2
                                                            percent = (range / data2)  * 100
                                                        }
                                                            if(isNaN(percent)) percent = 0
                                                     
                                                        return  y.toFixed(0) + " "  + "("+ percent.toFixed(2).replace(/\.00$/,'') + "%"+ ")";
                                                }else if(names === "No show"){
                                                    data1 = opts.w.globals.seriesLog[opts.seriesIndex][opts.dataPointIndex]  ;
                                                    data2 = opts.w.globals.seriesLog[parseInt(opts.seriesIndex)+1][opts.dataPointIndex]  ;
                                                     
                                                        if (data1===0 && data2 === 0) {
                                                            percent = 0
                                                        }else if(data2 === 0) {
                                                            let range = (data1 +1)  - (data2 +1)
                                                            let count = (range /(data2+1)) 
                                                            percent = count * 100
                                                        }else {
                                                            let range = data1  - data2
                                                            percent = (range / data2)  * 100
                                                        }
                                                            if(isNaN(percent)) percent = 0
                                                     
                                                    return  y.toFixed(0) + " "  + "("+ percent.toFixed(2).replace(/\.00$/,'') + "%"+ ")";
                                                }else if(names === "Late Cancel"){
                                                    data1 = opts.w.globals.seriesLog[opts.seriesIndex][opts.dataPointIndex]  ;
                                                    data2 = opts.w.globals.seriesLog[parseInt(opts.seriesIndex)+1][opts.dataPointIndex]  ;
                                                      
                                                        if (data1===0 && data2 === 0) {
                                                            percent = 0
                                                        }else if(data2 === 0) {
                                                            let range = (data1 +1)  - (data2 +1)
                                                            let count = (range /(data2+1)) 
                                                            percent = count * 100
                                                        }else {
                                                            let range = data1  - data2
                                                            percent = (range / data2)  * 100
                                                        }
                                                            if(isNaN(percent)) percent = 0
                                                     
                                                            return  y.toFixed(0) + " "  + "("+ percent.toFixed(2).replace(/\.00$/,'') + "%"+ ")" ;
                                                }else if(names === "Booking"){
                                                    data1 = opts.w.globals.seriesLog[opts.seriesIndex][opts.dataPointIndex]  ;
                                                    data2 = opts.w.globals.seriesLog[parseInt(opts.seriesIndex)+1][opts.dataPointIndex]  ;
                                                 
                                                        if (data1===0 && data2 === 0) {
                                                            percent = 0
                                                        }else if(data2 === 0) {
                                                            let range = (data1 +1)  - (data2 +1)
                                                            let count = (range /(data2+1)) 
                                                            percent = count * 100
                                                        }else {
                                                            let range = data1  - data2
                                                            percent = (range / data2)  * 100
                                                        }
                                                            if(isNaN(percent)) percent = 0
                                                     
                                                            return  y.toFixed(0) + " "  + "("+ percent.toFixed(2).replace(/\.00$/,'') + "%"+ ")";
                                                }
                                         
                                            }else {
                                                return y
                                            }
                                            }
                                        }

                                    }
                                    }
                                 }else {
                                this.series = [
                                    {
                                    name: "Attended",
                                    color: "#00b4d8",
                                    data: this.attended ? this.attended : []
                                    },
                                    {
                                        name: "Booking",
                                        color: "#2ec4b6",
                                        data: this.booking ? this.booking : []
                                    },
                                    {
                                        name: "Late Cancel",
                                        color: "#c9184a",
                                        data: this.cancel ? this.cancel : []
                                    },
                                    {
                                        name: "No show",
                                        color: "#ffa200",
                                        data: this.noshow ? this.noshow : []
                                    },
                                   
                                ]
                                this.chartOptions = {
                                    legend: {
                                        show:true,
                                        position: "bottom",
                                        horizontalAlign: "center",
                                        customLegendItems: ["Attended","Booking","Late Cancel","No show", ],
               
                                    },
                                    chart: {
                                        id: 'chartBarSeries',
                                        toolbar: {
                                            show: false
                                        },
                                        height: 350,
                                        type: 'line',
                                        zoom: {
                                            enabled: false
                                        }
                                    },
                                    dataLabels: {
                                        enabled: false
                                    },
                                    stroke: {
                                        curve: 'straight'
                                    },
                                    title: {
                                        align: 'left'
                                    },
                                    grid: {
                                        row: {
                                            colors: ['#f3f3f3', 'transparent'], 
                                            opacity: 0.5
                                        },
                                    },
                                    xaxis: {
                                        categories:this.formatted_date
                                    }
                            }
                          }
                        
                    }
                }, response => {})
            },

            getDate(){
                const moment = require('moment')
                this.dateRange.startDate = this.dateRange.startDate !== '' ? moment( this.dateRange.startDate).format('YYYY-MM-DD') : ''
                this.dateRange.endDate=  this.dateRange.endDate !== '' ? moment( this.dateRange.endDate).format('YYYY-MM-DD') : ''
                 this.dateRange2.startDate = this.dateRange2.startDate !== '' ? moment( this.dateRange2.startDate).format('YYYY-MM-DD') : ''
                this.dateRange2.endDate=  this.dateRange2.endDate !== '' ? moment( this.dateRange2.endDate).format('YYYY-MM-DD') : ''

                var params = {
                    thisWeek: this.thisWeek,
                    thisTwoWeeks : this.thisTwoWeeks,
                    thisMonth: this.thisMonth,
                    thisThreeMonth : this.thisThreeMonth,
                    thisSixMonth : this.thisSixMonth,
                    thisYear: this.thisYear,
                    start:  this.dateRange.startDate,
                    end:  this.dateRange.endDate,
                    thisWeek2: this.thisWeek,
                    thisTwoWeeks2 : this.thisTwoWeeks,
                    thisMonth2: this.thisMonth,
                    thisThreeMonth2 : this.thisThreeMonth,
                    thisSixMonth2 : this.thisSixMonth,
                    thisYear2: this.thisYear,
                    start2:  this.dateRange2.startDate,
                    "category[]": this.filter_category_id ? this.filter_category_id: "",
                    end2:  this.dateRange2.endDate,
                    sportsClassIDs: this.sportsClassIDs,
                }
                this.load_statistic = true
                this.$http.get(process.env.API_URL + 'member-session/status-statistic', {params: params}).then(response => {
                    this.load_statistic = false
                    var status = response.data.status
                    if(status){
                        if(this.thisThreeMonth === 1 || this.thisSixMonth === 1 || this.thisYear === 1){
                                this.formatted_date =  response.data.data.attended.date.map((element) => moment(element).locale("id").format('MMM YYYY')) 
                                var day = moment().format("DD")
                                this.dateRange.startDate = response.data.data.attended.date[0] 
                                this.dateRange.endDate = response.data.data.attended.date[response.data.data.attended.date.length - 1] + "-" + day
                                this.dateRange2.startDate = response.data.data['attended 2'].date[0]
                                this.dateRange2.endDate = response.data.data['attended 2'].date[response.data.data['attended 2'].date.length - 1]  + "-" + day
                        }else {
                                this.formatted_date =  response.data.data.attended.date.map((element) => moment(element).locale("id").format('ll')) 
                                this.dateRange.startDate = response.data.data.attended.date[0]
                                this.dateRange.endDate = response.data.data.attended.date[response.data.data.attended.date.length - 1]
                                this.dateRange2.startDate = response.data.data['attended 2'].date[0]
                                this.dateRange2.endDate = response.data.data['attended 2'].date[response.data.data['attended 2'].date.length - 1] 
                        }
                    }
                }, response => {})
            },
          
            getBookingList(page = null){
                const moment = require('moment')
                   this.dateRange.startDate = this.dateRange.startDate !== '' ? moment( this.dateRange.startDate).format('YYYY-MM-DD') : ''
                this.dateRange.endDate=  this.dateRange.endDate !== '' ? moment( this.dateRange.endDate).format('YYYY-MM-DD') : ''
                  this.filter_category_id = this.filter_category !== null  ?this.filter_category.id : ''

                  let status_attended = this.isattended ? "Attend" : ""
                  let status_booked = this.isbooking ? "Booked" : ""
                  let status_cancel = this.iscancel ? "Late Cancel" : ""
                  let status_nowshow = this.isnoshow ? "No Show" : ""
                  const status_filter = [status_attended, status_booked, status_cancel, status_nowshow].filter((item) => item)
                this.status_booking = status_filter.join(",");
                
                var params = {
                    thisWeek: this.thisWeek,
                    thisTwoWeeks : this.thisTwoWeeks,
                    thisMonth: this.thisMonth,
                    thisThreeMonth : this.thisThreeMonth,
                    thisSixMonth : this.thisSixMonth,
                    thisYear: this.thisYear,
                    start:  this.dateRange.startDate,
                    end:  this.dateRange.endDate,
                    category: this.filter_category_id,
                    page: page,
                    status : this.status_booking
                }
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'member-session', {params: params}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                        if(typeof this.$refs.paginate_ !== typeof undefined){
                            this.$refs.paginate_.selected = this.current_page - 1
                        }
                        
                    }
                }, response => {})
            },
            toggleFilter(){
                this.show_filter_dropdown = !this.show_filter_dropdown
            },
            
        },
    }
</script>

<style scoped>
    [date-range] > div{
        display: inline-block;
    }

    .box{
        padding: 9px 11px 7px 11px;
        background-color: #ef3b29;
        border-radius: 10px;
        color: #fff;
        margin-bottom: 1rem;
    }
    .box > label{
        display: block;
        font-family: 'gothambold';
        margin-bottom: 0;
    }
    .box > label:first-child{
        font-size: 12px;
    }
    .box > label:last-child{
        font-size: 18px;
    }

    .board-report{
        margin-left: -7px;
        margin-right: -7px;
        margin-bottom: 2rem;
    }
    .board-report .col{
        display: inline-block;
        width: 20%;
        padding-left: 7px;
        padding-right: 7px;
    }

    .select-filter{
        position: relative;
        display: inline-block;
        
    }
    .select-filter .input-select{
        width: 205px;
        border: solid 1px #ef3b29;
        padding: 7px 12px;
        color: #ef3b29;
        cursor: pointer;
    }
    .select-dropdown{
        display: none;
        position: absolute;
        top: 40px;
        width: 205px;
    }
    .select-dropdown.show{
        display: block;
        background-color: #fff;
        z-index: 1;
        border: solid 1px #ef3b29;
        padding: 15px 15px;
    }
    .select-dropdown .btn.small{
        min-width: 82px;
        margin-top: .5rem;
    }

    .class-filter{
        /* width: 750px; */
        margin-bottom: 1.5rem;
    }
    .class-filter .form-check{
        display: inline-block;
        width: 28%;
        margin-bottom: .4rem;
    }

    .btn-filter{
        border: none;
        /* border-radius: 25px; */
        background-color: #ef3b29;
        font-size: 16px;
        letter-spacing: 1px;
        color: #fff;
    }
    .btn-filter:hover{
        color: #fff;
        box-shadow: 0 0 5px 2px #ef3b29;
    }
    .btn-filter.inverse{
        /* border-radius: 25px; */
        background-color: transparent;
        color: #ef3b29;
        font-size: 14px;
        border: solid 1px #ef3b29;
    }
    .switch {
        position: relative;
        display: inline-block;
        width: 56px;
        height: 27px;
    }

    .switch input {
        display: none;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: 0.4s;
        transition: 0.4s;
    }

    .slider:before {
        position: absolute;
        content: "";
        height: 20px;
        width: 20px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: 0.4s;
        transition: 0.4s;
    }

    input:checked + .slider {
        background-color: #ef3b29;
    }

    input:focus + .slider {
        box-shadow: 0 0 1px #ef3b29;
    }

    input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }

    .slider.round {
        border-radius: 34px;
    }

    .slider.round:before {
        border-radius: 50%;
    }

    @media (max-width: 1199px) {
        .board-report .col{
            width: 24%;
        }
    }
    @media (max-width: 991px) {
        .board-report .col{
            width: 49%;
        }
        .class-filter{
            width: 100%;
        }
        .class-filter .form-check{
            width: 38%;
        }
    }
    @media (max-width: 767px) {
        .board-report .col{
            width: 49%;
        }
    }
    @media (min-width: 576px) and (max-width: 1303px) {
        [date-range] > label:first-child,
        [date-range] > div:last-child {
            display: block;
        }
    }
    @media (max-width: 576px) {
        [date-range] > div:nth-child(2),
        [date-range] > div:nth-child(4),
        [date-range] > div:last-child{
            display: block;
            width: 80%;
        }
        .board-report .col{
            width: 100%;
        }
        .class-filter .form-check{
            width: 90%;
        }
    }

    .row-filter {
        display: flex;
    
    }
    .disabled-filter {
        background: #606060;
    }

    .btn-apply {
        text-align: right;
        margin-top: 12px;
    }

    .column-filter {
        flex: 50%;
        justify-content: center;
    }
</style>



