<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">TRAINER</label>
            <label class="breadcrumb">Trainer List</label>
        </div>

        <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link :to="{ name: 'create_trainer', params: {} }" class="btn btn-main inverse small">+ CREATE TRAINER</router-link>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3 no-padding-left">
            <div class="form-group mb-45 ">
                <label class="mb-2">TYPE FILTER</label>
             
                  <multiselect v-model="selected_type" :options="type_options" :searchable="true"    :hide-selected="true" :show-no-results="true" track-by="id" label="name" :close-on-select="true" :show-labels="false" placeholder="SELECT TYPE"  @select="getListTeacher"></multiselect>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th>FULL NAME</th>
                                <th>TYPE</th>
                                <th>PRICE</th>
                                <th>LOYALTY POINTS</th>
                                <th>SPECIALIZATION</th>
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="7" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0">
                                <td colspan="7">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" :key="index" v-else>
                                <td>
                                    <router-link :to="{ name: 'detail_trainer', params: {id: item.id} }" class='link'>{{item.firstName}} {{item.lastName}}</router-link>
                                </td>
                                <td>
                                  <span v-if="item.category === 'personal trainer'">PT</span>
                                  <span v-if="item.category === 'class'">Class</span>
                                </td>
                                <td>
                                    <span v-if="item.price > 0">{{item.price | numberFormat}} CREDIT</span>
                                    <span v-else> - </span>
                                </td>
                             
                                <td>
                                    <span v-if="item.rewardPoints > 0">{{item.rewardPoints | numberFormat}}</span>
                                    <span v-else> - </span>
                                </td>
                                <td>
                                    <span v-if="item.teacherSpecialization.length > 0">
                                        <div class="dropdown">
                                            <a href="" class="black-table" data-toggle="dropdown">
                                                {{ item.teacherSpecialization.length }} ITEM{{ item.teacherSpecialization.length > 1 ? 'S' : '' }}
                                                <i class="fa fa-chevron-down red" aria-hidden="true"></i>
                                            </a>
                                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                                <ul>
                                                    <li v-for="(special,i) in item.teacherSpecialization" :key="i"> {{ special.teacherSpecialization.name }} </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </span>
                                    <span v-else> - </span>
                                </td>
                                 <td>
                                    <router-link :to="{ name: 'edit_trainer', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                    <button type="button" class="btn btn-main inverse small mt-1 mb-1" @click="showDeleteConfirmation(item, index)">DELETE</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>
          <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deleteTrainer()">
            <div slot="header">DELETE TRAINER</div>
            <div slot="body">
                are you sure you want to delete this trainer? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Paginate from 'vuejs-paginate'
    import debounce from 'debounce'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
    import Multiselect from 'vue-multiselect'
    import Confirmation from '@/components/Confirmation'
    export default {
        name: 'trainer-list',
        components: {
            'paginate': Paginate,
            'confirmation': Confirmation,
            Multiselect
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
         watch: {
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
        },
        data (){
            return {
                profile: {},
                delete_confirmation: false,
                keyword: '',
                sort_column: 'email', // * fullName * role * email * phoneNumber *
                sort_type: 'asc',
                is_loading: false,
                data: [],
                selected_type: { id: "", name:"All" },
                type_options: [{ id: "", name:"All" },{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal trainer"}],
                last_page: 0,
                current_page: 0,
                arrow: arrow,
                data_temp: {},
                index_temp: '',
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListTeacher(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            callbackPaginate: function(pageNum) {
                this.getListTeacher(true, pageNum, this.sort_column, this.sort_type)
            },
            search: debounce(function(){
                this.getListTeacher(false)
            },250),
            doSortTable(value){
                if(value == this.sort_column){
                    if(this.sort_type == 'asc'){
                        this.sort_type = 'desc'
                    } else {
                        this.sort_type = 'asc'
                    }
                } else {
                    this.sort_type = 'asc'
                }
                this.sort_column = value
                this.getListTeacher(true, null, this.sort_column, this.sort_type)
            },
            getListTeacher(loading, page = null, sortColumn = null, sortType = null){
                this.is_loading = loading
              
                this.$http.get(process.env.API_URL + 'teacher', {params: {'keyword': this.keyword, 'page': page, 'sortColumn': sortColumn, 'sortType': sortType, 'category': this.selected_type === null ? '' : this.selected_type.id} }).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data
                        this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                    }
                }, response => {})
            },
            showDeleteConfirmation(data, index){
                this.data_temp = data
                this.index_temp = index
                this.delete_confirmation = true
            },
            deleteTrainer(){
                this.$http.post(process.env.API_URL + 'teacher/delete/' + this.data_temp.id).then(response => {
                    this.delete_confirmation = false
                    document.documentElement.style.overflow = 'auto'
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>

</style>



