<template>
  <div class="">
    <div class="mb-4">
      <label class="page-header">ACTIVITY ROLE</label>
      <label class="breadcrumb">Activity List</label>
    </div>
     <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
          
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>
  
    <div class="row">
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>FULL NAME</th>
                <th>EMAIL</th>
                <th>ACTIVITY</th>
                <th>CREATED AT</th>
                       
              </tr>
            </thead>
            <tbody v-if="is_loading">
              <tr>
                <td colspan="7" class="text-center">
                  <span
                    ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i
                  ></span>
                </td>
              </tr>
            </tbody>
            <tbody v-else>
               <tr class="text-center" v-if="data.length <= 0">
                <td colspan="7">
                  <span>No matching records found</span>
                </td>
              </tr>
              <tr v-for="(item, index) in data" :key="index" v-else>
                 <td>
                  <span>{{ item.user.firstName }} {{ item.user.lastName }}</span>
                </td>
                 <td>
                  <span>{{ item.user.email }}</span>
                </td>
                <td>
                  <span>{{ item.activity }}</span>
                </td>
                  <td>
                  <span>{{ item.createdAt }}</span>
                </td>
               
                
            
              </tr>
              
            </tbody>
          </table>
        </div>

        <div class="">
          <nav
            aria-label="Page navigation"
            class="page navigation"
            v-if="last_page > 1"
          >
            <paginate
              :page-count="last_page"
              :click-handler="callbackPaginate"
              :prev-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :next-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :container-class="'pagination'"
              :page-class="'page-item'"
              :page-link-class="'page-link'"
              :prev-class="'page-item'"
              :prev-link-class="'page-link'"
              :next-class="'page-item'"
              :next-link-class="'page-link'"
              :page-range="5"
              :margin-pages="4"
              ref="paginate"
            >
            </paginate>
          </nav>
        </div>
      </div>
    </div>
  
  </div>
</template>

<script>
import { mapState } from "vuex";
import Paginate from "vuejs-paginate";
import debounce from "debounce";
import arrow from "@/assets/images/ic_arrow_square_hover.svg";
import Confirmation from "@/components/Confirmation";
import ConfirmationUpdate from "@/components/ConfirmationUpdate";
 import Multiselect from 'vue-multiselect'
export default {
  name: "report-list",
  components: {
    Multiselect,
    paginate: Paginate,
    confirmation: Confirmation,
    confirmationupdate: ConfirmationUpdate
  },
  computed: mapState(["user", "isAuthenticated"]),
  data() {
    return {
      profile: {},
      keyword: "",
      is_loading: false,
      data: [],
      // selected_status: { id: "", name:"All" },
      // status_options: [{ id: "", name:"All" },{id: "waiting", name: "Waiting"}, { id: "success",name: "Success"} , { id: "failed",name: "Failed"},{ id: "pending",name: "Pending"},{ id: "challenge",name: "Challenge"}, {id: "expired",name: "Expired"}],
      last_page: 0,
      current_page: 0,
      index_temp: "",
      arrow: arrow,
    };
  },
  mounted() {
    this.getAuthUser();
    this.getActivityRole();

  },
  methods: {
    getAuthUser() {
      if (this.isAuthenticated) {
        if (typeof this.user.id == typeof undefined || !this.user.id) {
          this.$http.get(process.env.API_URL + "user/profile").then(
            response => {
              this.profile = response.data.data;
            },
            response => {}
          );
        } else {
          this.profile = this.user;
        }
      }
    },
    callbackPaginate: function(pageNum) {
      this.getActivityRole(true, pageNum);
    },
    search: debounce(function() {
      this.getActivityRole(false);
    }, 250),
     getActivityRole(loading, page = null){  
        this.is_loading = loading;
        this.$http.get(process.env.API_URL + 'activity-log', {params: { page: page, 'keyword': this.keyword}}).then(response => {
             this.is_loading = false;
        var status = response.data.status
        if(status){
         this.data = response.data.data.data;
              this.last_page = response.data.data.last_page;
              this.current_page = response.data.data.current_page;
         }
      }, response => {})
    },
  }
};
</script>

<style lang="css" scoped>


.pointer {
  cursor: pointer;
  color: #00A6ED;
}
.class-filter{
  width: 750px;
  margin-bottom: 1.5rem;
}
.class-filter .form-check{
  display: inline-block;
  width: 30%;
  margin-bottom: .4rem;
}

@media (max-width: 1199px) {
  .board-report .col{
    width: 24%;
  }
}
@media (max-width: 991px) {
      
.class-filter{
  width: 100%;
 }
.class-filter .form-check{
  width: 38%;
 }
}
@media (max-width: 576px) {
.class-filter .form-check{
  width: 90%;
  }
}
</style>



