<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">VOUCHER</label>
            <label class="breadcrumb">Voucher List</label>
        </div>
    <div class="mb-45">
         <button type="button" v-if="selected_category.id === '2'" class="btn btn-tab inverse small mt-1 mb-2" @click="changeTab('3')" >PACKAGE</button>
        <button  v-else type="button" class="btn btn-tab active small mt-1 mb-2">PACKAGE</button>

         <button v-if="selected_category.id === '3'" type="button" class="btn btn-tab inverse small mt-1 mb-2" @click="changeTab('2')" >FIRST TIMER</button>
        <button v-else type="button" class="btn btn-tab active small mt-1 mb-2" >FIRST TIMER</button>
    </div>
        <div class="row justify-content-between align-items-center mb-2">
            <div  class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <router-link v-if="selected_category.id === '3'" :to="{ name: 'create_reward', params: {} }" class="btn btn-main inverse small">+ CREATE VOUCHER</router-link>

            </div>
        <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
       
                                <th>VOUCHER NAME</th>
                                <th>CODE</th>
                                <!-- <th>POINTS</th> -->
                                <th>EXPIRATION DATE</th>
                                <th style="width: 12%;">APPLIED TO</th>
                                <th style="width: 12%;">USED</th>
                                <th style="width: 24%;">ACTION</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="8" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data.length <= 0 && is_loading === false">
                                <td colspan="8">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                               <tr class="text-center" v-else-if="data.length <= 0 && is_loading">
                                <td colspan="8">
                                    <span><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data" :key="item.id" v-else>
                           
                                <td>{{item.name}}</td>
                                <td>{{item.code}}</td>
                           
                                <td>{{item.expired | dateFormatFull}}</td>
                                <td>
                                    <span v-if="item.memberRvs.length > 0"> 
                                        <div class="dropdown" v-if="item.type === 3" >
                                            <a href="" class="black-table" data-toggle="dropdown"> 
                                                {{ item.memberRvs.length }} MEMBER{{ item.memberRvs.length > 1 ? 'S' : '' }}
                                                <i class="fa fa-chevron-down red" aria-hidden="true"></i>
                                            </a>
                                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                                <ul>
                                                    <li v-for="packet in item.memberRvs" :key="packet.id"> {{ packet.firstName }} {{ packet.lastName }}   </li>
                                                </ul>
                                            </div>
                                        </div>
                                         <div class="dropdown" v-else >
                                            <a href="" class="black-table" data-toggle="dropdown"> 
                                                1 MEMBER
                                                <i class="fa fa-chevron-down red" aria-hidden="true"></i>
                                            </a>
                                            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                                <ul>
                                                    <li> {{ item.memberRvs[0].firstName }} {{ item.memberRvs[0].lastName }}   </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </span>
                                    
                                    <span v-else> - </span>
                                </td>
                          
                                <td>
                                    <a href="" class="link" @click.prevent="showUsedList(item)">View</a>
                                </td>
                                <td>
                                    <router-link :to="{ name: 'edit_reward', params: {id: item.id} }" class="btn btn-main inverse small mt-1 mb-1">EDIT</router-link>
                                    <button type="button" class="btn btn-main inverse small mt-1 mb-1" @click="showDeleteConfirmation(item, index)">DELETE</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                 <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                        <paginate
                            :page-count="last_page"
                            :click-handler="callbackPaginate"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
            </div>
        </div>

        <confirmation v-if="delete_confirmation" @close="delete_confirmation = false" @confirm="deletePackage()">
            <div slot="header">DELETE VOUCHER</div>
            <div slot="body">
                are you sure you want to delete this reward? this action can not be reverted
            </div>
        </confirmation>

         <div class="" used-list>
            <used-list :clickToClose="false" v-if="show_modal" @close="doCloseModal()">
                <div slot="header">VOUCHER USER LISTS</div>
                <div slot="body">
            <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
               
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search_used()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword_used" @keyup="search_used()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
            </div>
                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <th>FULL NAME</th>
                                <th>CODE</th>
                                <th>CATEGORY</th>
                                <th>CLASS</th>
                                <th>DATE SCHEDULE</th>
                                <th>VOUCHER NAME</th>
                                <th>EMAIL</th>
                                <th>DATE USED</th>
                             
                            </thead>
                            <tbody v-if="is_view_used_list">
                                <tr>
                                    <td colspan="6" class="text-center">
                                        <span ><i class="fa fa-spinner fa-pulse fa-fw"></i></span>
                                    </td>
                                </tr>
                            </tbody>
                            <tbody v-else>
                                <tr class="text-center" v-if="used_list.length <= 0">
                                    <td colspan="6">
                                        <span >No matching records found</span>
                                    </td>
                                </tr>
                                <tr v-for="item in used_list" :key="item.order_code" v-else>
                                    <td>
                                         {{item.firstName}} {{item.lastName}}
                                    </td>
                                    <td> {{ item.code }} </td>
                                    <td> 
                                        <span v-if="item.booking_session"> {{ item.booking_session }}
                                        </span>
                                        <span v-else>-</span>
                                    </td>
                                    <td v-if="item.booking_session">
                                        <span v-if="item.booking_session === 'personal trainer'">
                                        {{item.teacherFirstName}}  {{item.lastFirstName}}
                                       
                                        </span>
                                         <span v-else>
                                        {{item.sportsClassName}}
                                        </span>
                                    </td>
                                    <td v-else>-</td>
                                    <td> 
                                       
                                         <span v-if="item.session_start">  {{ item.session_start | dateTimeFormat }} 
                                        </span>
                                        <span v-else>-</span>
                                    </td>
                                    <td> {{ item.voucher_name }}</td>
                                    <td> {{ item.user_email }}</td>
                                    <td> 
                                        <span v-if="item.createdAt">  {{ item.createdAt | dateTimeFormat }} 
                                        </span>
                                        <span v-else>-</span>
                                      
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                     <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page_used > 1">
                        <paginate
                            :page-count="last_page_used"
                            :click-handler="callbackPaginateUsedVoucher"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
                </div>
            </used-list>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Paginate from 'vuejs-paginate'
    import debounce from 'debounce'
    import Confirmation from '@/components/Confirmation'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
    import Modal from '@/components/Modal'
 import Multiselect from 'vue-multiselect'

    export default {
        name: 'reward-list',
        components: {
            'paginate': Paginate,
            'confirmation': Confirmation,
            'used-list': Modal,
             Multiselect,
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
         watch: {
            show_modal: function() {
            if(this.show_modal){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
  },
        data (){
            return {
                profile: {},
                keyword: '',
                is_loading: false,
                data: [],
                last_page: 0,
                current_page: 0,
                show_confirmation: false,
                delete_confirmation: false,
                data_temp: {},
                index_temp: '',
                arrow: arrow,
                show_modal: false,
                is_view_used_list: false,
                last_page_used: 0,
                keyword_used: '',
                current_page_used: 0,
                selected_category: { id: "3", name:"Package" },
                category_options: [{ id: "3", name:"Package" },{id: "2", name: "First Time Package"}],
            }
        },
        mounted(){
            this.getAuthUser()
            this.getRewardList(true)
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            search: debounce(function(){
                this.getRewardList(false)
            },250),
            search_used: debounce(function(){
                this.showUsedList(null)
            },250),
             callbackPaginate: function(pageNum) {
                this.getRewardList(true, pageNum)
            },
            callbackPaginateUsedVoucher: function(pageNum) {
                this.showUsedList(null, pageNum)
            },
            changeTab(tab) {
                this.selected_category.id = tab
                this.getRewardList(true, 1)
            },
            showUsedList(item, page = null){
                this.show_modal = true
                this.is_view_used_list = true
                this.data_temp = item === null ? this.data_temp : item
                this.$http.get(process.env.API_URL + 'reward-voucher/redeemed/' + this.data_temp.id, {params: {'keyword': this.keyword_used,'page': page, 'limit' : 10}}).then(response => {
                    this.is_view_used_list = false
                    var status = response.data.status
                    if(status){
                        this.used_list = response.data.data.data
                        this.last_page_used = response.data.data.last_page
                        this.current_page_used = response.data.data.current_page
                    }
                }, response => {})
            },
            getRewardList(loading, page = null){
                this.is_loading = loading
                this.$http.get(process.env.API_URL + 'reward-voucher', {params: {'keyword': this.keyword, type:this.selected_category.id,'page': page}}).then(response => {
                    this.is_loading = false
                    this.data = response.data.data.data
                     this.last_page = response.data.data.last_page
                        this.current_page = response.data.data.current_page
                }, response => {})
            },
            showDeleteConfirmation(data, index){
                this.data_temp = data
                this.index_temp = index
                this.delete_confirmation = true
            },
            deletePackage(){
                this.$http.post(process.env.API_URL + 'reward-voucher/delete/' + this.data_temp.id).then(response => {
                    this.delete_confirmation = false
                    document.documentElement.style.overflow = 'auto'
                    var status = response.data.status
                    if(status){
                        this.data.splice(this.index_temp, 1)
                        this.$toastr('success', response.data.message)
                    } else {
                        this.$toastr('error', response.data.message)
                    }
                }, response => {})
            },
            doCloseModal(){
                this.show_modal = false
                this.data_temp  = {}
               
            },
        }
    }
</script>

<style lang="css" scoped>
    .table th, .table td{
        font-size: 11px;
    }

    .width-25 {
        width: 25% !important;
    }
    a.black-table:hover{
        color: #606060;
    }
    .dropdown-menu{
        left: initial !important;
        right: 0;
        background-image: linear-gradient(60deg, #404244 -20%, #242627 90%);
    }
    .dropdown-menu ul{
        padding-left: 28px;
        padding-right: 10px;
        margin-bottom: 0;
    }
    .dropdown-menu ul li{
        color: #fff;
        font-size: 11px;
        margin-bottom: 2px;
    }
    .btn-tab{
    background-color: #ef3b29;
    font-size: 16px;
    letter-spacing: 1px;
    color: #fff;
}
    .btn-tab:hover{
    color: #fff;
    box-shadow: 0 0 5px 2px #ef3b29;
}
    .btn-tab.inverse{
    background-color: transparent;
    color: #ef3b29;
    font-size: 14px;
    border: solid 1px #ef3b29;
}
  .btn-tab.active{
    background-color: #ef3b29;
    color: #ffffff;
    font-size: 14px;
    border: solid 1px #ef3b29;
}
  [used-list] /deep/ .modal-wrapper{
        width: 95%;
      
    }
</style>



