<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">DISCOUNTCODE</label>
            <label class="breadcrumb">Discountcode List/Create Discountcode</label>
        </div>
        <label class="page-title">CREATE DISCOUNTCODE</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label> NAME <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                    <div class="form-group mb-45">
                        <label>CODE <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.code" required>
                    </div>

                    <div class="row">
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>USAGE LIMIT <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.quota_type" id="usage_limit_type-unlimited" value="Unlimited">
                                        <label class="form-check-label" for="usage_limit_type-unlimited">
                                            Unlimited
                                        </label>
                                    </div>
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.quota_type" id="usage_limit_type-limited" value="Limited">
                                        <label class="form-check-label" for="usage_limit_type-limited">
                                            Limited
                                        </label>
                                    </div>
                                </div>
                                <div class="input-group back" v-if="form.quota_type == 'Limited'">
                                    <input type="text" class="form-control" v-model="form.quota" required>
                                    <span class="back">x</span>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>USAGE LIMIT PER USER  <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.limit_type" id="usage_limit_per_user_type-unlimited" value="Unlimited">
                                        <label class="form-check-label" for="usage_limit_per_user_type-unlimited">
                                            Unlimited
                                        </label>
                                    </div>
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.limit_type" id="usage_limit_per_user_type-limited" value="Limited">
                                        <label class="form-check-label" for="usage_limit_per_user_type-limited">
                                            Limited
                                        </label>
                                    </div>
                                </div>
                                <div class="input-group back" v-if="form.limit_type == 'Limited'">
                                    <input type="text" class="form-control" v-model="form.limit" required>
                                    <span class="back">x</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="row align-items-center">
                        <div class="col-12 col-md-5">
                            <div class="form-group mb-5">
                                <label>EXPIRATION DATE <span class="red">*</span> </label>
                                <div class="input-group front back">
                                    <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                    <span class="back icon" v-if="form.expired !== ''">
                                        <a href="#deleteDate" @click.prevent="form.expired = ''">
                                            <i class="fa fa-times" aria-hidden="true"></i>
                                        </a>
                                    </span>
                                </div>
                                <datetime v-model="form.expired" format="dd MMMM yyyy" input-class="form-control datetime front" class="theme light" :auto="true" :week-start="7"></datetime>
                            </div>
                        </div>
                    </div>

                     <div class="form-group mb-45">
                                <label>CATEGORY <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input @change="onChangeCategory" class="form-check-input" type="radio" v-model="category_packages" id="category_packages-credit" value="credit">
                                        <label class="form-check-label" for="category_packages-credit">
                                            Credit
                                        </label>
                                    </div>
                                    <div class="form-check form-check-inline mb-1">
                                        <input @change="onChangeCategory" class="form-check-input" type="radio" v-model="category_packages" id="category_packages" value="package">
                                        <label class="form-check-label" for="category_packages">
                                            Package
                                        </label>
                                    </div>
                                    <div class="form-check form-check-inline mb-1">
                                        <input @change="onChangeCategory" class="form-check-input" type="radio" v-model="category_packages" id="category_packages-none" value="none">
                                        <label class="form-check-label" for="category_packages-none">
                                            Credit & First Timer
                                        </label>
                                    </div>
                                </div>
                                
                    </div>

                    <div class="row">
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>DISCOUNT <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.isPercentage" id="discount_type-rp" value="0">
                                        <label class="form-check-label" for="discount_type-rp">
                                         Rp
                                        </label>
                                    </div>
                                    <div class="form-check form-check-inline mb-1">
                                        <input class="form-check-input" type="radio" v-model="form.isPercentage" id="discount_type-%" value="1">
                                        <label class="form-check-label" for="discount_type-%">
                                            %
                                        </label>
                                    </div>
                                </div>
                              
                                 <div>
                                    <div class="input-group front" v-if="form.isPercentage == '0'">
                                        <span class="front">Rp</span>
                                        <input type="text" class="form-control" v-model="form.value_currency" @keyup="formatPrice(form.value_currency)" required>
                                    </div>
                                    <div class="input-group back" v-else>
                                        <input type="text" class="form-control" maxlength="3" v-model="form.value_percentage" required>
                                        <span class="back">%</span>
                                    </div>
                                </div>
                                
                             
                            </div>
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>SHORT DESCRIPTION <span class="red">*</span> </label>
                        <textarea type="text" maxlength="30" class="form-control" v-model="form.description" required></textarea>
                    </div>

                    <div v-if="category_packages === 'package'" class="form-group mb-45">
                        <label class="mb-2">APPLIED TO</label>
                        <multiselect 
                        
                            v-model="form.packageIDs" 
                            placeholder="SELECT PACKAGE" 
                            label="name" track-by="id" 
                            :options="package_options" 
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="false"
                            :hide-selected="true" 
                            :show-no-results="true" 
                            >
                        </multiselect>
                    </div>
                    <div v-if="category_packages === 'credit'" class="form-group mb-45">
                        <label class="mb-2">APPLIED TO</label>
                        <multiselect 
                            v-model="form.creditIDs" 
                            placeholder="SELECT CREDIT" 
                            label="title" track-by="id" 
                            :options="credit_options" 
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true" 
                            :show-no-results="true" 
                            @search-change="getListCredit">
                        </multiselect>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'voucher', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Multiselect from 'vue-multiselect'

    export default {
        name: '',
        components: {
            Multiselect,
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                package_options: [],
                credit_options: [],
                category_packages: 'none',
                is_loading: false,
                isLoading: false,
                form: {
                    name: null,
                    code: null,
                    expired: null,
                    description: null,
                    quota_type: 'Unlimited',
                    quota: null,
                    limit_type: 'Unlimited',
                    limit: null,
                    isPercentage: 0,
                    value_currency: '',
                    value_percentage: '',
                    // type: 'package',
                    packageIDs: [],
                    creditIDs : []
                }
            }
        },
        mounted(){
            this.getAuthUser()
            this.getPackageList()
            this.getListCredit()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getPackageList(query = null){
                this.isLoading = true
                this.$http.get(process.env.API_URL + 'package', {params: {'keyword': query,  firstTimeOnly: 1}}).then(response => {
                    this.isLoading = false
                    var status = response.data.status
                    if(status){
                        this.package_options = response.data.data.data
                    }
                }, response => {})
            },
            getListCredit(query = null){
             this.isLoading = true

                this.$http.get(process.env.API_URL + 'credit-price', {params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.credit_options = response.data.data.data
                      
                    }
                }, response => {})
            },
            formatPrice(value){
                var price = parseInt(value.toString().replace(/\./g,''))
                this.form.value_currency = this.formatNumber(value)
            },
            formatNumber(value){
                var temp = value.toString().replace(/\./g,'')
                var result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },
            onChangeCategory() {
               if(this.category_packages === 'package'){
                    this.form.packageIDs = this.package_options
                    this.form.creditIDs = []
               }else if(this.category_packages === 'credit'){
                    this.form.packageIDs = []
               }else {
                    this.form.creditIDs = []
                    this.form.packageIDs = []
               }
            },
            doCreate(){
                var moment = require('moment')
                this.form.quota = this.form.quota_type == 'Unlimited' ? null : this.form.quota
                this.form.limit = this.form.limit_type == 'Unlimited' ? null : this.form.limit
                this.form.expired = moment(this.form.expired).format('YYYY-MM-DD')
                this.form.isPercentage = parseInt(this.form.isPercentage)
                this.form.value = this.form.isPercentage == 0 ? parseInt(this.form.value_currency.toString().replace(/\./g,'')) : this.form.value_percentage   
                             
                for(var i = 0; i < this.form.packageIDs.length; i++){
                    this.form.packageIDs[i] = this.form.packageIDs[i].id
                }
                for(var i = 0; i < this.form.creditIDs.length; i++){
                    this.form.creditIDs[i] = this.form.creditIDs[i].id
                }

                this.is_loading = true
                this.$http.post(process.env.API_URL + 'voucher', this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'voucher', params: {} })
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>

</style>



