<template lang="html">
    <div class="">
        <div class="mb-4">
            <label class="page-header">VOUCHER</label>
            <label class="breadcrumb">Edit Voucher</label>
        </div>
        <label class="page-title">EDIT VOUCHER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                 <div class="text-center" v-if="isLoading">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>
                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                    <div  class="form-group mb-45">
                        <label>VOUCHER NAME <span class="red">*</span> </label>
                        <input v-if="this.reward.type == '3'"  type="text" class="form-control" v-model="form.name" required>
                         <input v-else  type="text" class="form-control" v-model="form.name" disabled>
                    </div>

                     <div class="form-group mb-45">
                        <label>VOUCHER CODE <span class="red">*</span> </label>
                        <input v-if="this.reward.type == '3'"  type="text" class="form-control" v-model="form.code" required />
                         <input v-else  type="text" class="form-control" v-model="form.name" disabled>
                    </div>
                   
                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea v-if="this.reward.type == '3'"  type="text" maxlength="30" class="form-control" v-model="form.description" required />
                        <textarea v-else  type="text" maxlength="30" class="form-control" v-model="form.description" disabled />
                    </div>

                    <div v-if="this.reward.type == '3'" class="form-group mb-45">
                                    <label class="mb-2">SELECT LOCATION <span class="red">*</span></label>
                                    <multiselect 
                                    v-model="selected_location" 
                                    :options="location_options" 
                                    @search-change="getListLocation" 
                                    :searchable="true" 
                                    track-by="id" label="location_name" :close-on-select="true" :show-labels="false" placeholder="SELECT LOCATION"></multiselect>
                    </div>
                    
                    <div class="row align-items-center">
                        <div class="col-12 col-md-5">
                            <div class="form-group mb-5">
                                <label>EXPIRATION DATE <span class="red">*</span> </label>
                                <div class="input-group front back">
                                    <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                    <span class="back icon" v-if="form.expired !== ''">
                                        <a href="#deleteDate" @click.prevent="form.expired = ''">
                                            <i class="fa fa-times" aria-hidden="true"></i>
                                        </a>
                                    </span>
                                </div>
                                <datetime v-model="form.expired" format="dd MMMM yyyy" input-class="form-control datetime front" class="theme light" :auto="true" :week-start="7"></datetime>
                            </div>
                        </div>
                    </div>

             
               
                    <div class="row"  >
                        <div class="col-12 col-md-6">
                            <div v-if="this.reward.type == '3'" class="form-group mb-45">
                                <label>VOUCHER TYPE <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1" v-for="type_option in type_options">
                                        <input class="form-check-input" type="radio" v-model="type" :value="type_option.id" :id="'type-' + type_option.id" />
                                               <label class="form-check-label" :for="'type-' + type_option.id">
                                               {{ type_option.label }}
                                        </label>
                                    </div>
                                </div>
                            </div>
                            <div v-else class="form-group mb-45">
                                <label>VOUCHER TYPE <span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="radio-disabled form-check form-check-inline mb-1" v-for="type_option in type_options">
                                        <input type="radio" v-model="type" :value="type_option.id" :id="'type-' + type_option.id" disabled />
                                               <label class="form-check-disabled" :for="'type-' + type_option.id">
                                               {{ type_option.label }}
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="form-group mb-45" >
                        <label class="mb-2">APPLIED TO</label>
                        <multiselect 
                        v-if="this.reward.type == '3'" 
                            v-model="form.memberIDs" 
                            placeholder="SELECT MEMBER" 
                              :custom-label="labelfullname"
                            label="firstName" track-by="id" 
                            :options="member_options" 
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true" 
                            :show-no-results="true" 
                            @search-change="getListUser">
                
                                <template slot="singleLabel" slot-scope="{ option }">
                                    <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                                   <template slot="option" slot-scope="{ option }">
                                <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                        </multiselect>
                        <multiselect 
                            v-else 
                            disabled
                            class="multiselectdisabled"
                            v-model="form.memberIDs" 
                            placeholder="SELECT MEMBER" 
                              :custom-label="labelfullname"
                            label="firstName" track-by="id" 
                            :options="member_options" 
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true" 
                            :show-no-results="true" 
                            @search-change="getListUser">
                             
                                <template slot="singleLabel" slot-scope="{ option }">
                                    <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                                   <template slot="option" slot-scope="{ option }">
                                <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                        </multiselect>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'reward', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>



            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Multiselect from 'vue-multiselect'

    export default {
        name: '',
        props: ['id'],
        components: {
            Multiselect,
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                member_options: [],
                type: 'class',
                type_options: [
                    {id: 'class', label: 'Class'},
                    {id: 'recovery', label: 'Recovery'}
                ],
                reward: {},
                selected_location : null,
                selected_member : null,
                location_options: [],
                is_loading: false,
                isLoading: false,
                last_page : '',
                current_page : '',
                 form: {
                    code : '',
                    expired: '',
                    limit : 1,
                    name: '',
                    description: '',
                    memberIDs: [],
                    quota: 1,
                    // type: '2',
                    category: "",
                    locationID : ''
                }
              
            }
        },
        mounted(){
            this.getAuthUser()
            this.getReward()
            this.getListUser()
            this.getListLocation()
        },
        methods: {
             labelfullname({ firstName, lastName }) {
            return `${firstName} ${lastName}`;
                },
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getReward(){
                   this.isLoading = true
                this.$http.get(process.env.API_URL + 'reward-voucher/' + this.id).then(response => {
                       this.isLoading = false
                    var status = response.data.status
                    if(status){
                        this.reward = response.data.data
                        this.form.name = this.reward.name
                        this.form.code = this.reward.code
                        this.form.description = this.reward.description
                        // this.form = Object.assign({}, this.form, this.reward);
                        this.form.expired = this.reward.expired.toString().split(' ')[0] + 'T00:00:00.000Z'
                        this.form.limit = 1
                        this.form.quota = 1
                        this.selected_location = this.reward.location
                        this.type = this.reward.category
                        let i = 0
                        for (i ; i < this.reward.memberRvs.length; i++) { 
                            this.reward.memberRvs[i].id = this.reward.memberRvs[i].memberID;
                        }
                        this.form.memberIDs = this.reward.memberRvs
                    }
                   
                }, response => {})
            },
              getListUser( query = null,page = null){
             
                this.$http.get(process.env.API_URL + 'user/all', {params: {'keyword': query, 'page': page}}).then(response => {
                
                    var status = response.data.status
                    if(status){
                        this.member_options = response.data.data
                    }
                }, response => {})
            },
            getListLocation(query = null){
                this.$http.get(process.env.API_URL + 'location/all', {params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.location_options = response.data.data
                    }
                }, response => {})
            },
            formatPrice(value){
                var price = parseInt(value.toString().replace(/\./g,''))
                this.form.value = this.formatNumber(value)
            },
            formatNumber(value){
                var temp = value.toString().replace(/\./g,'')
                var result = temp.replace(/[^0-9,]/g, "").replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1.")
                return result
            },

            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.form.image = files[0];
            },
            doUpdate(){
               var moment = require('moment');
                var formData = new FormData();
                formData.set('expired', moment(this.form.expired).format('YYYY-MM-DD'));
                formData.set('code', this.form.code);
                formData.set('name', this.form.name);
                formData.set('limit', this.form.limit);
                formData.set('description', this.form.description);
                formData.set('quota', this.form.quota);
           
                formData.set('category', this.type);
                formData.set('locationID', this.selected_location ? this.selected_location.id : '');
          
                 formData.append('memberIDs', `[${this.form.memberIDs.map(e => e.id)}]`)
                this.is_loading = true
                this.$http.post(process.env.API_URL + 'reward-voucher/update/' + this.id, formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.$router.push({ name: 'reward', params: {} })
                    }
                }, response => {})
            }
        }
    }
</script>

<style lang="css" scoped>
/* .form-check-label {
    color: #606060 !important;
} */

 .radio-disabled [type="radio"]:checked,
.radio-disabled[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
}
.radio-disabled[type="radio"]:checked + label,
.radio-disabled[type="radio"]:not(:checked) + label
{
    position: relative;
    padding-left: 20px;
    cursor: pointer;
    line-height: 20px;
    display: inline-block;
    color: #606060;
    font-size: 13px;
}
.radio-disabled[type="radio"]:checked + label:before,
.radio-disabled[type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    width: 14px;
    height: 14px;
    border: 1px solid #979797;
    border-radius: 100%;
    background: #fff;
}
.radio-disabled [type="radio"]:checked + label:after,
.radio-disabled [type="radio"]:not(:checked) + label:after {
    content: '';
    width: 8px;
    height: 8px;
    background: #606060;
    position: absolute;
    top: 6px;
    left: 3px;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
.radio-disabled[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}
.radio-disabled[type="radio"]:checked + label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}
.radio-disabled[type="radio"]:disabled + label{
    cursor: not-allowed;
}


.multiselectdisabled >>> .multiselect__tags {
     border-color: #f4f4f4 !important; 
}

.multiselectdisabled >>> .multiselect__tag {
     background-color: #979797 !important; 
}

.multiselectdisabled >>> .multiselect__tag-icon:after {
     color: transparent !important; 
}
</style>



