<template>
    <section>
        <div class="container">
            <div>
                <img src="~@/assets/images/ic_logo_white.svg" class="logo" alt="">
            </div>
            <div class="row justify-content-center">
                <div class="col-10 col-md-8 col-lg-7">
                    <h1 class="title">CONTENT MANAGEMENT SYSTEM</h1>
                    <form class="white text-left" @submit.prevent="doLogin()" login>
                        <div class="form-group">
                            <div class="form-group margin-bottom-30">
                                <label>EMAIL</label>
                                <input type="email" class="form-control login" v-model="form.email" required>
                            </div>
                            <div class="form-group margin-bottom-60">
                                <label>PASSWORD</label>
                                <input type="password" class="form-control login" v-model="form.password" required>
                            </div>
                            <div class="row justify-content-center">
                                <div class="col-11 col-md-8 col-lg-6">
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-main btn-full font-bold" :disabled="is_loading">
                                            <span v-if="!is_loading">LOGIN</span>
                                            <span class="loading" v-else>
                                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                                            </span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    export default {
        name: 'auth-login',
        data(){
            return{
                is_loading: false,
                form: {
                    email: '',
                    password: '',
                }
            }
        },
        methods: {
            doLogin(){
                this.is_loading = true
                this.$store.dispatch('login', {user: this.form}).then((response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.replace({name: 'dashboard', params: {}})
                    }
                }), response => {})
            }
        }
    }
</script>

<style scoped>
    section{
        min-height: 100vh;
        display: flex;
        align-items: center;
        background-image: linear-gradient(45deg, #404244 -20%, #242627 90%);
    }
    section > div{
        width: 100%;
        padding-top: 60px;
        padding-bottom: 30px;
    }
    h1.title{
        font-size: 24px;
        color: #fff;
        margin-bottom: 40px;
        margin-top: 40px;
        letter-spacing: 8.05px;
    }
    .form-control{
        font-size: 1rem;
        height: calc(2.25rem + 2px);
    }
    @media (max-width: 576px) {
        .logo{
            width: 150px;
        }
        h1.title{
            font-size: 16px;
        }
        label{
            font-size: 12px;
        }
    }
</style>



