<template>
    <div class="cr-banner">
        <div class="mb-4">
            <label class="page-header">Banner</label>
            <label class="breadcrumb">Banner List/Create Banner</label>
        </div>
        <label class="page-title">CREATE BANNER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>BANNER IMAGE <span class="red">* (347 x 200px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="file_name">{{file_name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile" class="form-control hidden-input" accept="image/*" required>
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>BANNER TITLE</label>
                        <input type="text" class="form-control" v-model="form.name">
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.description" required>
                    </div>
                    <div class="form-group mb-45">
                        <label>LINK <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.link" required>
                    </div>

                    <div class="form-group mb-5">
                        <label class="mb-2">PRIORITY <span class="red">*</span></label>
                        <div class="row">
                            <div class="col-6 col-sm-4 mb-2">
                                <multiselect v-model="form.priority" :options="priority_options" :searchable="false" :close-on-select="true" :show-labels="false" placeholder="Choose Priority"></multiselect>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'banner', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Multiselect from 'vue-multiselect'

    export default {
        name: '',
        components: {
            Multiselect
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                is_loading: false,
                file: '',
                file_name: '',
                priority_options: [1,2,3,4,5],
                form: {
                    image: '',
                    name: '',
                    priority: '',
                    description: '',
                    link:''
                },
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListBanner()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                            this.checkAuthority()
                        }, response => {})
                    } else{
                        this.profile = this.user
                        this.checkAuthority()
                    }
                }
            },
            getListBanner(){
                this.$http.get(process.env.API_URL + 'banner').then(response => {
                    var status = response.data.status
                    if(status){
                        var data = response.data.data
                        for(var i = 0; i < data.length; i++){
                            this.priority_options.push(i + 1)
                        }
                        this.priority_options.push(data.length + 1)
                    }
                }, response => {})
            },
            checkAuthority(){
                if(this.profile.isOperator){
                    this.$toastr('error', "You don't have authorization to access this page!")
                    this.$router.push({ name: 'banner', params: {} })
                }
            },
            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.file = files[0];
                this.file_name = files[0].name
            },
            doCreate(){
                this.is_loading = true

                var formData = new FormData();
                formData.append('image', this.file)
                formData.append('title', this.form.name)
                formData.append('description', this.form.description)
                formData.append('link', this.form.link)
                formData.append('priority', this.form.priority)
                

                this.$http.post(process.env.API_URL + 'banner', formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'banner', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            }
        }
    }
</script>

<style>
.cr-banner #null-5.multiselect__element {
    display: none !important;
}
</style>



