<template>
  <div class="">
  <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3 no-padding-left">
            <div class="form-group mb-45 ">
                <label class="mb-2">PAYMENT STATUS</label>
                  <multiselect v-model="selected_status" :options="status_options" :searchable="true"    :hide-selected="true" :show-no-results="true" track-by="id" label="name" :close-on-select="true" :show-labels="false" placeholder="SELECT STATUS"  @select="filterByStatus"></multiselect>
            </div>
      </div>
    <div class="row">
        <div class="col-12 col-md-12 col-lg-12">
            <div class="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                            <th>TRANSACTION CODE</th>
                            <th>MEMBER</th>
                            <th>TRANSACTION DATE</th>
                            <th>PACKAGE</th>
                            <th style="width: 14%;">TOTAL CREDIT</th>
                            <th>PRICE</th>
                            <th>DISCOUNT</th>     
                            <th>DISCOUNTCODE</th>     
                            <th>TOTAL</th>  
                              <th>PAYMENT TYPE</th>      
                            <th>STATUS</th>    
                        </tr>
                    </thead>
                    <tbody v-if="is_loading">
                        <tr>
                            <td colspan="7" class="text-center">
                                <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                            </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr class="text-center" v-if="data.length <= 0">
                            <td colspan="7">
                                <span >No matching records found</span>
                            </td>
                        </tr>
                          <tr v-for="(item, index) in data" v-else>
                 <td>
                  <span>{{ item.code }}</span>
                </td>
                 <td>
                  <span>{{ item.fullName }}</span>
                </td>
                <td>
                  <span>{{ item.transactionDate }}</span>
                </td>
                <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.name}}   </div>    
                </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    CREDIT </div>    
                </td>
                 <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.credit}}   </div>    
              </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    {{credit.credit}}  </div>    
                </td>
                  <td v-if="item.packageOrders.length > 0"> 
                                <div v-for="packet in item.packageOrders" :key="packet.id"> 
                                    {{packet.package.price | currencyFormat }}  </div>    
              </td>
                <td v-else> 
                                <div v-for="credit in item.creditOrders" :key="credit.id"> 
                                    {{credit.price | currencyFormat}}  </div>    
                </td>
                 <td>
                  <span>{{ item.totalDiscount | currencyFormat }}</span>
                </td>
                 <td>
                 <span>{{ item.promoCode }}</span>
                </td>
                 <td>
                 <span>{{ item.total | currencyFormat }}</span>
                </td>
                <td>
                 <span v-if="item.paymentType">{{ item.paymentType }}</span>
                 <span v-else>
                    -
                 </span>
                </td>
                  <td>
                  <span>{{ item.status }} </span>
                </td>
              </tr>
                    </tbody>
                </table>
            </div>

            <div class="">
                <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                    <paginate
                        :page-count="last_page"
                        :click-handler="callbackPaginate"
                        :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :container-class="'pagination'"
                        :page-class="'page-item'"
                        :page-link-class="'page-link'"
                        :prev-class="'page-item'"
                        :prev-link-class="'page-link'"
                        :next-class="'page-item'"
                        :next-link-class="'page-link'"
                        :page-range="5"
                        :margin-pages="4"
                        ref="paginate">
                    </paginate>
                </nav>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
    import Paginate from 'vuejs-paginate'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
 import Multiselect from 'vue-multiselect'

    export default {
        name: 'purchase-history',
        props: ['id'],
        components: {
            'paginate': Paginate,
            Multiselect,
        },
        data (){
            return {
                is_loading: false,
                data: [],
                selected_status: { id: "", name:"All" },
                status_options: [{ id: "", name:"All" },{id: "waiting", name: "Waiting"}, { id: "success",name: "Success"} , { id: "failed",name: "Failed"},{ id: "pending",name: "Pending"},{ id: "challenge",name: "Challenge"}, {id: "expired",name: "Expired"}],
                last_page: 0,
                current_page: 0,
                arrow: arrow,
            }
        },
        mounted(){
            this.request()
        },
        methods: {
            callbackPaginate: function(pageNum) {
                this.request(pageNum)
            },
            request(page = null){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'member/transaction-log', {params: { 'page': page ,limit :10,'memberID': this.id,  status : this.selected_status === null ? '' : this.selected_status.id}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data.data ? response.data.data.data : response.data.data
                        this.last_page = response.data.data.last_page ? response.data.data.last_page : 0
                        this.current_page = response.data.data.current_page ? response.data.data.current_page : 0
                    }
                }, response => {})
            },
            filterByStatus() {
                this.request(1)
            }
        }
    }
</script>

<style scoped>
    .table td{
        vertical-align: top;
    }
</style>



