<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">SPORT CLASS TYPE</label>
            <label class="breadcrumb">Sport Class Type/Create Sport Class Type</label>
        </div>
        <label class="page-title">CREATE SPORT CLASS TYPE</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>IMAGE <span class="red">* (20 x 20px)</span> </label>
                        <div class="upload-wrapper continue mt-2">
                            <button type="button" class="btn btn-upload btn-main small">UPLOAD IMAGE</button>
                            <span class="ellipsis" v-if="file_name">{{file_name}}</span>
                            <span v-else class="ellipsis">No Choosen File</span>
                            <input type="file" value="" @change="onChangeFile" class="form-control hidden-input" accept="image/*" required>
                        </div>
                    </div>

                    <div class="form-group mb-45">
                        <label>NAME <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'sport_type', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: 'create-location',
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                file_name: '',
                files: {},
                is_loading: false,
                form: {
                    name:'',
                    image:null
                }
            }
        },
        mounted(){
            // this.getAuthUser()
        },
        methods: {
        
            doCreate(){
                this.is_loading = true
                let formData = new FormData();
                formData.append('image', this.form.image);
                formData.append('name', this.form.name);

                this.$http.post(process.env.API_URL + 'sport-class-type', formData).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'sport_type', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
            onChangeFile(event, attribute, defaultValue) {
                let files = event.target.files || event.dataTransfer.files;
                this.files = files[0]
                this.file_name = files[0].name
                this.form.image= files[0]
            },
        }
    }
</script>

<style lang="css" scoped>
    @media (max-width: 375px) {
        .btn.small{
            min-width: 100px
        }
    }
</style>



