<template>
    <div class="">
        <div class="row justify-content-between align-items-center mb-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3">
                <button type="button" class="btn btn-main small" @click="showModal()">Book a Member</button>
            </div>
            <div class="col-12 col-sm-6 col-md-5 col-lg-3 mb-3">
                <form class="" @submit.prevent="search()">
                    <div class="form-group search">
                        <input type="text" class="form-control small search" placeholder="search" v-model="keyword" @keyup="search()">
                        <span class="icon-search">
                            <img src="~@/assets/images/ic_search.svg" alt="">
                        </span>
                    </div>
                </form>
            </div>
        </div>

        <div class="row">
            <div class="col-12">
                <div class="table-responsive">
                    <table class="table">
                        <thead>
                            <tr>
                                <th>FULL NAME</th>
                                <th>PHONE NUMBER</th>
                                <th>EMAIL</th>
                                <th>EMERGENCY CONTACT NAME</th>
                                <th>EMERGENCY CONTACT PHONE NO.</th>
                                <th style="width: 30%">ATTENDANCE</th>
                            </tr>
                        </thead>
                        <tbody v-if="is_loading">
                            <tr>
                                <td colspan="6" class="text-center">
                                    <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr class="text-center" v-if="data_users.length <= 0">
                                <td colspan="6">
                                    <span >No matching records found</span>
                                </td>
                            </tr>
                            <tr v-for="(item, index) in data_users" :key="item.id" v-else>
                                <td>
                                    <router-link :to="{ name: 'detail_user', params: {id: item.memberPackage.member.user.id} }" class='link'>
                                        {{item.memberPackage.member.user.firstName}} {{item.memberPackage.member.user.lastName}}
                                    </router-link>
                                </td>
                                <td> {{ item.memberPackage.member.phone }} </td>
                                <td> {{ item.memberPackage.member.user.email }} </td>
                                <td> {{ item.memberPackage.member.emergencyName }} </td>
                                <td> {{ item.memberPackage.member.emergencyPhone }} </td>
                                <td>
                                    <button v-if="item.theDay" type="button" class="btn btn-main small mt-1 mb-1" :class="{'btn-green': item.status == 'Attend', 'inverse': item.status == 'Booked' || item.status != 'Attend'}" @click="fillClassAttendance(item, index, 'Attend')" :disabled="item.status == 'Cancel' || item.status == 'Late Cancel'">Attended</button>

                                    <button type="button" class="btn btn-main small mt-1 mb-1" :class="{'btn-red': item.status == 'No Show', 'inverse': item.status == 'Booked' || item.status != 'No Show'}" @click="fillClassAttendance(item, index, 'No Show')" :disabled="item.status == 'Cancel' || item.status == 'Late Cancel'">No Show</button>

                                    <button type="button" class="btn btn-main small mt-1 mb-1" :class="{'btn-orange': item.status == 'Cancel' || item.status == 'Late Cancel', 'inverse': item.status == 'Booked' || (item.status != 'Cancel' && item.status !== 'Late Cancel')}" @click="showCancelConfirmation(item, index)" :disabled="item.status == 'Attend' || item.status == 'No Show' || item.status == 'Cancel'">
                                        <span v-if="item.status !== 'Late Cancel'">Cancel</span>
                                        <span v-else>Late Cancel</span>
                                    </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>

        <div book-member>
            <book v-if="show_modal" @close="doCloseModal()">
                <div slot="header" style="width: 100%;">
                    <div class="row">
                        <div class="col-12 col-sm-6 text-left">BOOK A MEMBER</div>
                        <div class="col-12 col-sm-6 text-right">
                            <button class="btn btn-main small" type="button" @click="putOnBooked()" :disabled="is_put_on_booked">
                                ADD MEMBER ({{ member_added.length }})
                                <span v-if="is_put_on_booked">
                                    <i class="fa fa-spin fa-spinner"></i>
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
                <div slot="body">
                    <div class="row text-left mb-3">
                        <div class="col-12 col-sm-6 col-md-3">
                            <form class="" @submit.prevent="memberSearch()">
                                <div class="form-group search">
                                    <input type="text" class="form-control small search" placeholder="Search Member" v-model="modal_keyword" @keyup="memberSearch()">
                                    <span class="icon-search">
                                        <img src="~@/assets/images/ic_search.svg" alt="">
                                    </span>
                                </div>
                            </form>
                        </div>
                    </div>

                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <th>FULL NAME</th>
                                <th>PHONE NUMBER</th>
                                <th>EMAIL</th>
                                <th>ADD MEMBER</th>
                            </thead>
                            <tbody v-if="is_load_member">
                                <tr>
                                    <td colspan="4" class="text-center">
                                        <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                                    </td>
                                </tr>
                            </tbody>
                            <tbody v-else>
                                <tr class="text-center" v-if="members.length <= 0">
                                    <td colspan="4">
                                        <span >No matching records found</span>
                                    </td>
                                </tr>
                                <tr v-for="(item, index) in members" :key="item.id" v-else>
                                    <td> {{ item.firstName }} {{ item.lastName }} </td>
                                    <td> {{ item.member.phone }} </td>
                                    <td> {{ item.email }} </td>
                                    <td>
                                        <div class="checkbox">
                                            <input type="checkbox" :id="'shareable' + index" v-model="item.checked" @change="onChangeCheckbox(item, index)" :disabled="!item.checked && !is_allowed_to_add" />
                                            <label class="red font-bold" :for="'shareable' + index">
                                                <div class=""><i class="fa fa-check"></i></div>
                                            </label>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="">
                    <nav aria-label="Page navigation" class="page navigation" v-if="last_page_used > 1">
                        <paginate
                            :page-count="last_page_used"
                            :click-handler="callbackPaginateActiveMember"
                            :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                            :container-class="'pagination'"
                            :page-class="'page-item'"
                            :page-link-class="'page-link'"
                            :prev-class="'page-item'"
                            :prev-link-class="'page-link'"
                            :next-class="'page-item'"
                            :next-link-class="'page-link'"
                            :page-range="5"
                            :margin-pages="4"
                            ref="paginate">
                        </paginate>
                    </nav>
                </div>
                </div>
            </book>
        </div>

        <confirmation v-if="cancel_confirmation" @close="cancel_confirmation = false" @confirm="cancelBookingMember()">
            <div slot="header">CANCEL BOOKING</div>
            <div slot="body">
                are you sure you want to cancel this booking of member? this action can not be reverted
            </div>
        </confirmation>
    </div>
</template>

<script>
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
import Confirmation from '@/components/Confirmation'
import Modal from '@/components/Modal'
import debounce from 'debounce'
import Paginate from 'vuejs-paginate'
import { mapState } from 'vuex'

    export default {
        name: 'detail-user',
        props: ['id'],
        components: {
            'book': Modal,
            'confirmation': Confirmation,
            'paginate': Paginate,
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        watch: {
           show_modal: function() {
            if(this.show_modal){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
        },
        data(){
            return{
                IMAGE_URL: process.env.IMAGE_URL,
                profile: {},
                is_loading: false,
                cancel_confirmation: false,
                keyword: '',
                data_users: [],
                data_image: '',
                show_modal: false,
                modal_keyword: '',
                item_temp: {},
                index_temp: '',
                is_load_member: false,
                is_allowed_to_add: true,
                max_addition: 0,
                remain_addition: 0,
                members: [],
                member_added: [],
                is_put_on_booked: false,
                last_page_used: 0,
                current_page_used: 0,
                arrow: arrow,
            }
        },
        mounted(){
            this.getAuthUser()
            this.getListUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            search: debounce(function(){
                this.getListUser()
            },250),
            getListUser(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'session/booking-list/' + this.id, {params: {'keyword': this.keyword}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data_users = response.data.data
                    }
                }, response => {})
            },

            showCancelConfirmation(item, index){
                if(item.status !== 'Cancel'){
                    this.cancel_confirmation = true
                    this.item_temp = item
                    this.index_temp = index
                }
            },
            cancelBookingMember(){
                this.$http.post(process.env.API_URL + 'member-session/cancel', {memberSessionID: this.item_temp.id}).then(response => {
                    this.cancel_confirmation = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.data_users[this.index_temp].status = response.data.data.status
                        this.$parent.subtrackSlotTaken()
                    }
                }, response => {})
            },
            fillClassAttendance(item, index, attendance){
                     this.is_loading = true
                this.$http.post(process.env.API_URL + 'member-session/update/' + item.id, {status: attendance}).then(response => {
                         this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.data_users[index].status = attendance
                    }
                }, response => {})
            },

            showModal(){
                this.show_modal = true
                this.max_addition = this.$parent.getMaximumAdditionBookedMember()
                this.remain_addition = this.$parent.getMaximumAdditionBookedMember()
                this.getActiveMember()
            },
            doCloseModal(){
                this.show_modal = false
                this.member_added = []
            },
            memberSearch: debounce(function(){
                this.getActiveMember()
            },250),
            callbackPaginateActiveMember: function(pageNum) {
                this.getActiveMember( pageNum)
            },
            getActiveMember(page = null){
                this.is_load_member = true
                this.$http.get(process.env.API_URL + 'session/available-member/' + this.id, {params: {'keyword': this.modal_keyword, page:page, limit:10}}).then(response => {
                    this.is_load_member = false
                    var status = response.data.status
                    if(status){
                        this.members = response.data.data.data
                          this.last_page_used = response.data.data.last_page
                        this.current_page_used = response.data.data.current_page
                        for(var i = 0; i < this.members.length; i++){
                            var member = this.members[i]
                            if (this.member_added.filter(function(e) { return e.id === member.id; }).length > 0) {
                                this.members[i].checked = true
                            } 
                        }
                    }
                }, response => {})
            },
            onChangeCheckbox(item, index){
                if(item.checked && this.remain_addition > 0 && this.remain_addition <= this.max_addition){
                    this.member_added.push(item)
                    this.remain_addition -= 1
                } else {
                    if (this.member_added.filter(function(e) { return e.id === item.id; }).length > 0) {
                        var index = this.member_added.findIndex(e => e.id === item.id)
                        this.member_added.splice(index, 1)
                        this.remain_addition += 1
                    }
                }
                
                if(this.member_added.length == this.max_addition){
                    this.is_allowed_to_add = false
                } else {
                    this.is_allowed_to_add = true
                }
            },
            putOnBooked(){
                var formData = new FormData()
                formData.append('sessionID', this.id)

                for(var i = 0; i < this.member_added.length; i++){
                    formData.append('memberID[' + i + ']', this.member_added[i].id)
                }

                this.is_put_on_booked = true
                this.$http.post(process.env.API_URL + 'member-session/book', formData).then(response => {
                    this.is_put_on_booked = false
                    var status = response.data.status
                    if(status){
                        this.show_modal = false
                        for(var i = 0; i < this.member_added.length; i++){
                            this.$parent.addSlotTaken()
                        }
                        for(var i = 0; i < response.data.data.length; i++){
                            this.data_users.push(response.data.data[i])
                        }
                        this.member_added = []
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    .btn.small.mt-1{
        min-width: 80px;
    }
    .btn:disabled{
        opacity: .5;
        cursor: not-allowed;
    }
    .table td{
        font-family: 'gothambook';
        padding: 1.4rem .3rem;
    }

    [book-member] /deep/ .modal-wrapper{
        width: 70%;
    }
</style>



