<template>
    <div class="row">
        <div class="col-12 col-md-12 col-lg-8">
            <div class="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                            <th>PACKAGE TYPE</th>
                          
                            <th>EXPIRATION DATE</th>
                            <th>EDIT</th>
                        </tr>
                    </thead>
                    <tbody v-if="is_loading">
                        <tr>
                            <td colspan="4" class="text-center">
                                <span ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i></span>
                            </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr class="text-center" v-if="data.length <= 0">
                            <td colspan="4">
                                <span >No matching records found</span>
                            </td>
                        </tr>
                        <tr v-for="(item, index) in data" :key="item.id" v-else>
                            <td> {{ item.package.name }} </td>
                        
                            <td> {{ item.expired | dateFormat }} </td>
                            <td>
                                <button type="button" class="btn btn-main inverse small" @click="showForm(item, index)">EDIT</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="">
                <nav aria-label="Page navigation" class="page navigation" v-if="last_page > 1">
                    <paginate
                        :page-count="last_page"
                        :click-handler="callbackPaginate"
                        :prev-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :next-text="'<img src=' + arrow + ' width=' + 17 +'>'"
                        :container-class="'pagination'"
                        :page-class="'page-item'"
                        :page-link-class="'page-link'"
                        :prev-class="'page-item'"
                        :prev-link-class="'page-link'"
                        :next-class="'page-item'"
                        :next-link-class="'page-link'"
                        :page-range="5"
                        :margin-pages="4"
                        ref="paginate">
                    </paginate>
                </nav>
            </div>
        </div>

        <form-modal v-if="show_form" @close="doCloseForm()">
            <div slot="header">EDIT ACTIVE PACKAGE</div>
            <div slot="body">
                <form @submit.prevent="doEdit()">
                    <div class="modal-body">
                        <div class="form-group mb-4">
                            <label class="mb-2">PACKAGE TITLE</label>
                            <input type="text" class="form-control" v-model="item_temp.package.name" disabled>
                        </div>
                        <div class="row justify-content-center">
                 
                            <div class="col-12 col-md-12">
                                <div class="form-group mb-5">
                                    <label class="mb-2">EXPIRATION DATE</label>
                                    <div class="input-group front back">
                                        <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                        <span class="back icon" v-if="item_temp.expired !== ''">
                                            <a href="#deleteDate" @click.prevent="item_temp.expired = ''">
                                                <i class="fa fa-times" aria-hidden="true"></i>
                                            </a>
                                        </span>
                                    </div>
                                    <datetime type="date" v-model="item_temp.expired" format="dd MMMM yyyy" value-zone="Asia/Jakarta" :input-class="{'form-control datetime front': true, 'error': has_error}" class="theme light" :auto="true" :week-start="7" required></datetime>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="modal-footer">
                        <button type="submit" class="btn btn-main small" :class="{'disabled': save_data}">
                            SAVE
                            <span class="btn-loading" v-if="save_data"><i class="fa fa-spinner fa-spin fa-fw"></i></span>
                        </button>
                        <button type="button" class="btn btn-main inverse small" @click="doCloseForm()">
                            CANCEL
                        </button>
                    </div>
                </form>
            </div>
        </form-modal>
    </div>
</template>

<script>
    import { Datetime } from 'vue-datetime'
    import Form from '@/components/Form'
    import arrow from '@/assets/images/ic_arrow_square_hover.svg'
        import Paginate from 'vuejs-paginate'

    export default {
        name: 'active-package',
        props: ['id'],
        components: {
            'form-modal': Form,
            'datetime': Datetime,
            'paginate': Paginate
        },
        data () {
            return {
                is_loading:false,
                data: [],
                 last_page: 0,
                current_page: 0,
                session_date: '',
                has_error: false,

                show_form: false,
                item_temp: {},
                index_temp: '',
                save_data: false,
                 arrow: arrow
            }
        },
        mounted(){
            this.request()
        },
        methods: {
             callbackPaginate: function(pageNum) {
                this.request(pageNum)
            },
            request(page = null){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'member/active-package', {params: {'memberID': this.id, 'page': page}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                           this.last_page = response.data.last_page ? response.data.last_page : 0
                        this.current_page = response.data.current_page ? response.data.current_page : 0
                    }
                }, response => {})
            },
            showForm(item, index){
                this.show_form = true
                this.item_temp = JSON.parse(JSON.stringify(item))
                this.index_temp = index
            },
            doCloseForm(){
                this.show_form = false
            },
            doEdit(){
                this.save_data = true
                var moment = require('moment')
                var form = {
                    credit: this.item_temp.credit,
                    expired: moment(this.item_temp.expired).format('YYYY-MM-DD')
                }
                if(this.item_temp.package.firstTimeOnly === 1) {
                    this.$http.post(process.env.API_URL + 'member-package/update-first-time/' + this.item_temp.id, form).then(response => {
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.data[this.index_temp].credit = response.data.data.credit
                        this.data[this.index_temp].expired = response.data.data.expired
                        this.save_data = false
                        this.show_form = false
                    }
                }, response => {})
                }else {
                    this.$http.post(process.env.API_URL + 'member-package/update/' + this.item_temp.id, form).then(response => {
                    var status = response.data.status
                    if(status){
                        this.$toastr('success', response.data.message)
                        this.data[this.index_temp].credit = response.data.data.credit
                        this.data[this.index_temp].expired = response.data.data.expired
                        this.save_data = false
                        this.show_form = false
                    }
                }, response => {})
                }
               
            },
        }
    }
</script>

<style lang="css" scoped>

</style>



