// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

require('bootstrap/dist/css/bootstrap.min.css')
require('bootstrap/dist/js/bootstrap.min.js')
require('font-awesome/css/font-awesome.min.css')
require('vue-multiselect/dist/vue-multiselect.min.css')
require('@deveodk/vue-toastr/dist/@deveodk/vue-toastr.css')
require('vue-datetime/dist/vue-datetime.css')
require('./assets/css/main.css')
require('./assets/js/smoothscroll.js')
require('./assets/js/primary.js')

import VueToastr from '@deveodk/vue-toastr'
import axios from 'axios'
import vMediaQuery from 'v-media-query'
import Vue from 'vue'
import VueApexCharts from 'vue-apexcharts'
import VueAxios from 'vue-axios'
import VueObserveVisibility from 'vue-observe-visibility'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import store from './store'

import DateRangePicker from 'vue2-daterange-picker'

Vue.use(VueApexCharts)
Vue.use(DateRangePicker)
Vue.use(VueObserveVisibility)
Vue.use(VueAxios, axios)
Vue.use(Vuex)
Vue.use(vMediaQuery)
Vue.use(VueToastr, {
    defaultTitle: '',
    defaultPosition: 'toast-top-right',
    defaultType: 'info',
    defaultClickClose: true,
    defaultTimeout: 3000
})

Vue.config.productionTip = false
Vue.axios.options.emulateJSON = true
Vue.axios.options.emulateHTTP = true

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})



