<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
    export default {
        name: 'App',
        beforeCreate() {
            this.axios.interceptors.request.use(
                request => {
                    return request;
                }
            );
            this.axios.interceptors.response.use(
                response => {
                    var status = response.data.status
                    if(!status){
                        var message = response.data.message
                        var errors = response.data.errors
                        this.$toastr('error', message)
                        for(var key in errors) {
                             this.$toastr('error', errors[key])
                            // var error = errors[key];
                            // if(error.length) {
                            //     for(var i = 0; i < error.length; i++){
                            //         this.$toastr('error', error[i])
                            //     }
                            // }
                        }
                    }
                    return response;
                },
                error => {
                    switch (error.response.status) {
                        case 401:
                            this.$store.dispatch('logout', {requestOptions: {method: 'GET'}}).then(() => {
                                this.$toastr('error', error.response.data.message)
                                this.$router.push({name: 'root', params: ''})
                            });
                            break;
                        case 404:
                            var message = error.response.data.message
                            var errors = error.response.data.errors
                            this.$toastr('error', message)
                            for(var key in errors) {
                                var error = errors[key];
                                if(error.length) {
                                    for(var i = 0; i < error.length; i++){
                                        this.$toastr('error', error[i])
                                    }
                                }
                            }
                            break;
                        case 400:
                            this.$toastr('error', error.response.data.message)
                            break;
                        case 500:
                            this.$toastr('error', "Internal Server Error, Silahkan coba lagi!!!")
                            break;
                        case 504:
                            this.$toastr('error', "Timeout, Silahkan coba lagi!!!")
                            break;
                        default:
                            break;
                    }
                    return error.response;
                }
            );
        },
    }
</script>

<style>

</style>



