<template>
  <div class="">
    <div class="mb-4">
      <label class="page-header">CUSTOMER FEEDBACK</label>
      <label class="breadcrumb">Customer Feedback List</label>
    </div>
      <div class="col-12 col-sm-6 col-md-5 col-lg-4 col-xl-3 mb-3 no-padding-left">
            <div class="form-group mb-45 ">
                <label class="mb-2">CATEGORY FILTER</label>
                  <multiselect v-model="selected_category" :options="category_options" :searchable="true"    :hide-selected="true" :show-no-results="true" track-by="id" label="name" :close-on-select="true" :show-labels="false" placeholder="SELECT CATEGORY"  @select="getListComments"></multiselect>
            </div>
      </div>
  
      
      <!-- <div>
        <label v-if="isClass" class="mr-3 mb-3"> CLASS FILTER </label>
        <div class="class-filter">
            <div class="form-check" v-for="(item, index) in classes" :key="item.id">
                <div class="checkbox">
                    <input type="checkbox" v-model="sportsClassIDs" :value="item.id" :id="item.id" @change="onChangeFilterClass(item, index)"/>
                    <label class="red" :for="item.id">
                        <div class=""><i class="fa fa-check"></i></div> {{ item.name }}
                    </label>
                </div>
            </div>
      </div>
         <label  v-if="isTeacher" class="mr-3 mb-3"> TEACHER FILTER </label>
         <div class="class-filter">
            <div class="form-check" v-for="(item, index) in teachers" :key="item.id">
                <div class="checkbox">
                    <input type="checkbox" v-model="teacherID" :value="item.id" :id="item.id" @change="onChangeFilterClass(item, index)"/>
                    <label class="red" :for="item.id">
                        <div class=""><i class="fa fa-check"></i></div>
                        {{ item.firstName }} {{ item.lastName }}
                    </label>
                </div>
            </div>
        </div>
     
      </div> -->
     
 

    <div class="row">
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>ID</th>
                <th style="width: 12%;">MEMBER NAME</th>
                <th style="width: 14%;">FROM</th>
                <th>COMMENTS</th>
                <th>ISSHOWED</th>
                <th style="width: 12%;">CREATED AT</th>
                <th style="width: 12%;">ACTION</th>
              </tr>
            </thead>
            <tbody v-if="is_loading">
              <tr>
                <td colspan="7" class="text-center">
                  <span
                    ><i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i
                  ></span>
                </td>
              </tr>
            </tbody>
            <tbody v-else>
              <tr class="text-center" v-if="data.length <= 0">
                <td colspan="7">
                  <span>No matching records found</span>
                </td>
              </tr>
              <tr v-for="(item, index) in data" :key="index" v-else>
                <td>
                  <span>{{ item.id }}</span>
                </td>
                <td>
                  <span>{{ item.firstName }} {{ item.lastName }}</span>
                </td>
               <td>
                  <span>{{ item.sportsClassName === null ? item.teacherNameFirst + ' ' + item.teacherNamelast : item.sportsClassName }}</span>
                </td>
                <td>
                  <span v-if="item.comments">{{ item.comments }}</span>
                    <span v-else>-</span>
                </td>
                <td>
                  
                  <span class="pointer" @click="showEditConfirmation(item, index)">{{ item.isShowed === 0 ? "False" : "True" }}</span>
                  
                </td>
                <td>{{ item.createdAt | dateFormat }}</td>
                <td>
            
                  <button
                    type="button"
                    class="btn btn-main inverse small mt-1 mb-1"
                    @click="showDeleteConfirmation(item, index)"
                  >
                    DELETE
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <div class="">
          <nav
            aria-label="Page navigation"
            class="page navigation"
            v-if="last_page > 1"
          >
            <paginate
              :page-count="last_page"
              :click-handler="callbackPaginate"
              :prev-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :next-text="'<img src=' + arrow + ' width=' + 17 + '>'"
              :container-class="'pagination'"
              :page-class="'page-item'"
              :page-link-class="'page-link'"
              :prev-class="'page-item'"
              :prev-link-class="'page-link'"
              :next-class="'page-item'"
              :next-link-class="'page-link'"
              :page-range="5"
              :margin-pages="4"
              ref="paginate"
            >
            </paginate>
          </nav>
        </div>
      </div>
    </div>
    <confirmation
      v-if="delete_confirmation"
      @close="delete_confirmation = false"
      @confirm="deleteCustomerFeedback()"
    >
      <div slot="header">DELETE CUSTOMER FEEDBACK</div>
      <div slot="body">
        are you sure you want to delete this customer feedback? this action can
        not be reverted
      </div>
    </confirmation>
    <confirmationupdate
      v-if="update_confirmation"
      @close="update_confirmation = false"
      @confirm="doUpdate()"
    >
      <div slot="header">ISSHOWED</div>
      <div slot="body">
         
        <div class="mt-2">
            <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isShowed"
                      id="discount_type-rp"
                      value="0"
                    />
                    <label class="form-check-label" for="discount_type-rp">
                      False
                    </label>
                  </div>
                  <div class="form-check form-check-inline mb-1">
                    <input
                      class="form-check-input"
                      type="radio"
                      v-model="form.isShowed"
                      id="discount_type-%"
                      value="1"
                    />
                    <label class="form-check-label" for="discount_type-%">
                      True
                    </label>
                  </div>
                </div>
      </div>
    </confirmationupdate>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Paginate from "vuejs-paginate";
import debounce from "debounce";
import arrow from "@/assets/images/ic_arrow_square_hover.svg";
import Confirmation from "@/components/Confirmation";
import ConfirmationUpdate from "@/components/ConfirmationUpdate";
 import Multiselect from 'vue-multiselect'
export default {
  name: "customer-feedback-list",
  components: {
     Multiselect,
    paginate: Paginate,
    confirmation: Confirmation,
    confirmationupdate: ConfirmationUpdate
  },
  computed: mapState(["user", "isAuthenticated"]),
  watch: {
            update_confirmation: function() {
            if(this.update_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            },
            delete_confirmation: function() {
            if(this.delete_confirmation){
                document.documentElement.style.overflow = 'hidden'
            return
            }
            document.documentElement.style.overflow = 'auto'
            }
  },
  data() {
    return {
      profile: {},
      classes: [],
      teachers: [],
      sportsClassIDs: [],
      isClass: false,
      isTeacher: false,
      teacherID: [],
      keyword: "",
      sort_column: "email", 
      sort_type: "asc",
      is_loading: false,
      data: [],
      selected_category: { id: "", name:"All" },
      category_options: [{ id: "", name:"All" },{id: "class", name: "Class"}, { id: "personal trainer",name: "Personal Trainer"} , { id: "wellness",name: "Wellness"},{ id: "recovery",name: "Recovery"}],
      last_page: 0,
      current_page: 0,
      delete_confirmation: false,
      update_confirmation: false,
      data_temp: {},
      index_temp: "",
      arrow: arrow,
       form: {
        isShowed: ""
      }
    };
  },
  mounted() {
    this.getAuthUser();
    this.getListComments(true);
  },
  methods: {
    getAuthUser() {
      if (this.isAuthenticated) {
        if (typeof this.user.id == typeof undefined || !this.user.id) {
          this.$http.get(process.env.API_URL + "user/profile").then(
            response => {
              this.profile = response.data.data;
            },
            response => {}
          );
        } else {
          this.profile = this.user;
        }
      }
    },
    callbackPaginate: function(pageNum) {
      this.getListComments(true, pageNum, this.sort_column, this.sort_type);
    },
    search: debounce(function() {
      this.getListComments(false);
    }, 250),
    doSortTable(value) {
      if (value == this.sort_column) {
        if (this.sort_type == "asc") {
          this.sort_type = "desc";
        } else {
          this.sort_type = "asc";
        }
      } else {
        this.sort_type = "asc";
      }
      this.sort_column = value;
      this.getListComments(true, null, this.sort_column, this.sort_type);
    },
     getClassList(){
        this.$http.get(process.env.API_URL + 'sports-class', {params: {'keyword': this.keyword}}).then(response => {
        var status = response.data.status
        if(status){
          this.isClass = true
            this.classes = response.data.data.data
         }
      }, response => {})
    },
    getRecoveryList(){
        this.$http.get(process.env.API_URL + 'recovery', {params: {'keyword': this.keyword}}).then(response => {
        var status = response.data.status
        if(status){
          this.isClass = true
            this.classes = response.data.data.data
         }
      }, response => {})
    },
    getWellnessList(){
        this.$http.get(process.env.API_URL + 'wellness', {params: {'keyword': this.keyword}}).then(response => {
        var status = response.data.status
        if(status){
          this.isClass = true
            this.classes = response.data.data.data
         }
      }, response => {})
    },
    getTeacherList(){
        this.$http.get(process.env.API_URL + 'teacher', {params: {'keyword': this.keyword}}).then(response => {
        var status = response.data.status
        if(status){
           this.isTeacher = true
            this.teachers = response.data.data.data
         }
      }, response => {})
    },

    getListComments(loading, page = null, sortColumn = null, sortType = null) {
      this.is_loading = loading;
    
      if(this.selected_category !== null && this.selected_category.id === 'class'){
          this.getClassList()
          this.getTeacherList()
      }else if(this.selected_category !== null && this.selected_category.id === 'recovery'){
          this.getRecoveryList()
          this.teachers = []
          this.isTeacher = false
      
      }else if(this.selected_category !== null && this.selected_category.id === 'wellness'){
          this.getWellnessList()
          this.teachers = []
          this.isTeacher = false
      }else if(this.selected_category !== null && this.selected_category.id === 'personal trainer') {
        this.isClass = false
        this.classes = []
          this.getTeacherList()
      }else {
        this.isTeacher = false
        this.isClass = false
        this.classes = []
        this.teachers = []
      }

      this.$http
        .get(process.env.API_URL + "comments", {
          params: {
            category: this.selected_category === null ? '' : this.selected_category.id,
            sportsClassID : this.sportsClassIDs.length > 0 ? this.sportsClassIDs.toString() : '',
            teacherID : this.teacherID.length > 0 ? this.teacherID.toString(): '',
            page: page,
            sortColumn: sortColumn,
            sortType: sortType
          }
        })
        .then(
          response => {
            this.is_loading = false;
            var status = response.data.status;
            if (status) {
              this.data = response.data.data.data;
              this.last_page = response.data.data.last_page;
              this.current_page = response.data.data.current_page;
            }
          },
          response => {}
        );
    },
     onChangeFilterClass(item, index){
                this.getListComments()
    },
    showDeleteConfirmation(data, index) {
      this.data_temp = data;
      this.index_temp = index;
      this.delete_confirmation = true;
    },
    showEditConfirmation(data, index) {
      this.form.isShowed = data.isShowed
      this.data_temp = data;
      this.index_temp = index;
      this.update_confirmation = true;
    },
    doUpdate() {
      this.$http
        .post(process.env.API_URL + "comments/update/" + this.data_temp.id, this.form)
        .then(
          response => {
              this.update_confirmation = false;
              document.documentElement.style.overflow = 'auto'
            var status = response.data.status;
            if (status) {
            
              this.getListComments(true);
              this.$toastr("success", response.data.message);
            }else {
              this.$toastr("error", response.data.message);
            }
          },
          response => {}
        );
    },
    deleteCustomerFeedback() {
      this.$http
        .post(process.env.API_URL + "comments/delete/" + this.data_temp.id)
        .then(
          response => {
            this.delete_confirmation = false;
            document.documentElement.style.overflow = 'auto'
            var status = response.data.status;
            if (status) {
              this.data.splice(this.index_temp, 1);
              this.$toastr("success", response.data.message);
            } else {
              this.$toastr("error", response.data.message);
            }
          },
          response => {}
        );
    }
  }
};
</script>

<style lang="css" scoped>

.pointer {
  cursor: pointer;
  color: #00A6ED;
}
.class-filter{
  width: 750px;
  margin-bottom: 1.5rem;
}
.class-filter .form-check{
  display: inline-block;
  width: 30%;
  margin-bottom: .4rem;
}

@media (max-width: 1199px) {
  .board-report .col{
    width: 24%;
  }
}
@media (max-width: 991px) {
      
.class-filter{
  width: 100%;
 }
.class-filter .form-check{
  width: 38%;
 }
}
@media (max-width: 576px) {
.class-filter .form-check{
  width: 90%;
  }
}
</style>



