import vueAuthInstance from '../services/auth.js'

import Layout from '@/layouts/Layout'

import Login from '@/pages/auth/Login'
import Dashboard from '@/pages/dashboard/Dashboard'

import CreateUser from '@/pages/user/Create'
import DetailUser from '@/pages/user/Detail'
import EditUser from '@/pages/user/Edit'
import User from '@/pages/user/Index'
import BookingHistory from '@/pages/user/components/Book'
import ActiveCredit from '@/pages/user/components/Credit'
import ActivePackage from '@/pages/user/components/Package'
import PurchaseHistory from '@/pages/user/components/Purchase'
import TransactionHistory from '@/pages/user/components/Transaction'

import CreateTrainer from '@/pages/trainer/Create'
import DetailTrainer from '@/pages/trainer/Detail'
import EditTrainer from '@/pages/trainer/Edit'
import Trainer from '@/pages/trainer/Index'

import CreateTrainerSpecialization from '@/pages/trainerSpecialization/Create'
import EditTrainerSpecialization from '@/pages/trainerSpecialization/Edit'
