import EditWellness from '@/pages/wellness/Edit'
import Wellness from '@/pages/wellness/Index'

import CreateRecovery from '@/pages/recovery/Create'
import DetailRecovery from '@/pages/recovery/Detail'
import EditRecovery from '@/pages/recovery/Edit'
import Recovery from '@/pages/recovery/Index'

import CreateCustomerFeedback from '@/pages/customerfeedback/Create'
import EditCustomerFeedback from '@/pages/customerfeedback/Edit'
import CustomerFeedback from '@/pages/customerfeedback/Index'

import CreateClass from '@/pages/class/Create'
import DetailClass from '@/pages/class/Detail'
import EditClass from '@/pages/class/Edit'
import Class from '@/pages/class/Index'

import DetailPackage from '@/pages/package/Detail'
import EditPackage from '@/pages/package/Edit'
import Package from '@/pages/package/Index'

import DetailSchedule from '@/pages/schedule/Detail'
import Schedule from '@/pages/schedule/Index'
import BookingList from '@/pages/schedule/components/BookingList'
import WaitingList from '@/pages/schedule/components/WaitingList'

import CreateVoucher from '@/pages/voucher/Create'
import EditVoucher from '@/pages/voucher/Edit'
import Voucher from '@/pages/voucher/Index'

import CreateReward from '@/pages/reward/Create'
import EditReward from '@/pages/reward/Edit'
