Vue.use(Router)

const user = localStorage.getItem("user")
const role = JSON.parse(user)

const router = new Router({
    mode: 'history',
    linkExactActiveClass: "active",
    linkActiveClass: "active",

    routes: [
        { path: '/', name: 'root', component: Login, props: true, meta: { auth: false, title: '', to: 'dashboard' } },
        {
            path: '', component: Layout,
            children: [
                { path: '/dashboard', name: 'dashboard', component: Dashboard, props: true, meta: { auth: true, title: '' }, },

                {
                    path: '/users', name: 'users', component: User,
                    beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    },
                    props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/users/create', name: 'create_user', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateUser, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/users/edit-user/:id', name: 'edit_user',
                    beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditUser, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/users/detail-user/:id', name: 'detail_user', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, props: true, component: DetailUser,
                    children: [
                        { path: 'active-package', name: 'active_package', props: true, component: ActivePackage, meta: { auth: true, title: '' } },
                        { path: 'booking-history', name: 'booking_history', props: true, component: BookingHistory, meta: { auth: true, title: '' } },
                        { path: 'purchase-history', name: 'purchase_history', props: true, component: PurchaseHistory, meta: { auth: true, title: '' } },
                        { path: 'transaction-history', name: 'transaction_history', props: true, component: TransactionHistory, meta: { auth: true, title: '' } },
                        { path: 'active_credit', name: 'active_credit', props: true, component: ActiveCredit, meta: { auth: true, title: '' } },
                    ]
                },

                {
                    path: '/class', name: 'class', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: Class, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/class/create', name: 'create_class', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: CreateClass, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/class/edit-class/:id', name: 'edit_class', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditClass, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/class/detail-class/:id', name: 'detail_class', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: DetailClass, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/package', name: 'package', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: Package, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/package/edit-package/:id', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, name: 'edit_package', component: EditPackage, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/package/detail-package/:id', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, name: 'detail_package', props: true, component: DetailPackage
                },

                {
                    path: '/schedule/:date', name: 'schedule', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin || role.isFrontdeskAdmin) {
                            next()
                        }
                    }, component: Schedule, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/schedule/:date/detail/:id', component: DetailSchedule, beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin || role.isFrontdeskAdmin) {
                            next()
                        }
                    }, props: true,
                    children: [
                        { path: '', name: 'detail_schedule', props: true, redirect: { name: 'booking_list' } },
                        { path: 'booking-list', name: 'booking_list', props: true, component: BookingList, meta: { auth: true, title: '' } },
                        { path: 'waiting-list', name: 'waiting_list', props: true, component: WaitingList, meta: { auth: true, title: '' } }
                    ]
                },

                {
                    path: '/banner', name: 'banner', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    },
                    component: Banner, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/banner/create', name: 'create_banner', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: CreateBanner, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/banner/edit-banner/:id', name: 'edit_banner', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditBanner, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/discountcode', name: 'voucher', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: Voucher, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/discountcode/create', name: 'create_voucher', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateVoucher, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/discountcode/edit-discountcode/:id', name: 'edit_voucher', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditVoucher, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/voucher', name: 'reward', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: Reward, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/voucher/create', name: 'create_reward', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateReward, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/voucher/edit-voucher/:id', name: 'edit_reward', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditReward, props: true, meta: { auth: true, title: '' }
                },

                // { path: '/achievement', name: 'achievement', component: Achievement, props: true, meta: { auth: true, title: '' } },
                // { path: '/achievement/create', name: 'create_achievement', component: CreateAchievement, props: true, meta: { auth: true, title: '' } },
                // { path: '/achievement/edit-achievement/:id', name: 'edit_achievement', component: EditAchievement, props: true, meta: { auth: true, title: '' } },
                // { path: '/achievement/detail-achievement/:id', name: 'detail_achievement', component: DetailAchievement, props: true, meta: { auth: true, title: '' } },

                {
                    path: '/trainer', name: 'trainer', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: Trainer, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/trainer/create', name: 'create_trainer', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: CreateTrainer, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/trainer/edit-trainer/:id', name: 'edit_trainer', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: EditTrainer, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/trainer/detail-trainer/:id', name: 'detail_trainer', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, props: true, component: DetailTrainer
                },

                {
                    path: '/specialization', name: 'specialization', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: TrainerSpecialization, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/specialization/create', name: 'create_specialization', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: CreateTrainerSpecialization, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/specialization/edit-specialization/:id', name: 'edit_specialization', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isCoachAdmin) {
                            next()
                        }
                    }, component: EditTrainerSpecialization, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/location', name: 'location', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: Location, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/location/create', name: 'create_location', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateLocation, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/location/edit-location/:id', name: 'edit_location', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditLocation, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/credit-price', name: 'credit_price', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreditPrice, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/credit-price/create', name: 'create_credit_price', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateCreditPrice, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/credit-price/edit-credit-price/:id', name: 'edit_credit_price', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditCreditPrice, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/sport-type', name: 'sport_type', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: SportClassType, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/sport-type/create', name: 'create_sport_type', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: CreateSportClassType, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/sport-type/edit-sport-type/:id', name: 'edit_sport_type', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditSportClassType, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/wellness', name: 'wellness', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: Wellness, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/wellness/create', name: 'create_wellness', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: CreateWellness, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/wellness/edit-wellness/:id', name: 'edit_wellness', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditWellness, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/wellness/detail-wellness/:id', name: 'detail_wellness', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, props: true, component: DetailWellness
                },

                {
                    path: '/recovery', name: 'recovery', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: Recovery, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/recovery/create', name: 'create_recovery', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: CreateRecovery, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/recovery/edit-recovery/:id', name: 'edit_recovery', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditRecovery, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/recovery/detail-recovery/:id', name: 'detail_recovery', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, props: true, component: DetailRecovery
                },

                {
                    path: '/customer-feedback', name: 'customer_feedback', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CustomerFeedback, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/customer-feedback/create', name: 'create_customer_feedback', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: CreateCustomerFeedback, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/customer-feedback/edit-customer-feedback/:id', name: 'edit_customer_feedback', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditCustomerFeedback, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/terms-and-conditions', name: 'terms_and_conditions', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: Terms, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/terms-and-conditions/edit-terms-and-conditions/:id', name: 'edit_terms_and_conditions', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditTnC, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/terms-and-conditions/edit-proof-of-hustlers/:id', name: 'edit_proof_of_hustler', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: EditPOH, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/report', name: 'report', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: Report, props: true, meta: { auth: true, title: '' }
                },
                {
                    path: '/report/detail-report/:category/:id', name: 'detail_report', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: DetailReport, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/transaction', name: 'transaction', component: Transaction, beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/activityrole', name: 'activity_role', beforeEnter(to, from, next) {
                        if (role.isAdmin) {
                            next()
                        }
                    }, component: ActivityRole, props: true, meta: { auth: true, title: '' }
                },

                {
                    path: '/subcontent/edit-subcontent/:id', name: 'edit_subcontent', beforeEnter(to, from, next) {
                        if (role.isAdmin || role.isContentAdmin) {
                            next()
                        }
                    }, component: EditSubcontent, props: true, meta: { auth: true, title: '' }
                },


            ]
        }
    ]
})

router.beforeEach(function (to, from, next) {
    if (to.meta && to.meta.title) {
        document.title = to.meta.title
    }

    if (to.meta && to.meta.auth !== undefined) {
        if (to.meta.auth) {
            if (vueAuthInstance.isAuthenticated()) {
                next()
            } else {
                router.replace({ name: 'root' })
            }
        } else {
            if (vueAuthInstance.isAuthenticated()) {
                router.replace({ name: to.meta.to })
            } else {
                next()
            }
        }
    } else {
        next()
    }
})

export default router



