<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">TRAINER SPECIALIZATION</label>
            <label class="breadcrumb">Trainer Specialization List/Create Trainer Specialization</label>
        </div>
        <label class="page-title">CREATE NEW TRAINER SPECIALIZATION</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>NAME <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'specialization', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'

    export default {
        name: 'create-specialization',
        components: {
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                is_loading: false,
                form: {
                    name: '',
                }
            }
        },
        mounted(){
            this.getAuthUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            doCreate(){
                this.is_loading = true
                this.$http.post(process.env.API_URL + 'teacher-specialization', this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.$router.push({ name: 'specialization', params: {} })
                        this.$toastr('success', response.data.message)
                    }
                }, response => {})
            },
        }
    }
</script>

<style lang="css" scoped>
    @media (max-width: 375px) {
        .btn.small{
            min-width: 100px
        }
    }
</style>



