<template>
    <div>
        <div class="mb-4">
            <label class="page-header">TRAINER</label>
            <label class="breadcrumb">Trainer List/Trainer Detail</label>
        </div>
        <label class="page-title mr-4">TRAINER PROFILE</label>
  

        <div class="detail-profile mt-4">
            <div class="row mb-4">
                <div v-if="data.category === 'personal trainer'" class="col-12 col-md-12 col-lg-5 mb-3 text-center">
                    <template v-if="!is_loading">
                        <img :src="data.imageUrl" :alt="data.image" class="img-fluid mb-4">
                    </template>
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw" v-else></i>
                </div>
                <div class="col-12 col-md-12 col-lg-7">
                    <div class="mb-4">
                        <label class="title">Full Name</label>
                        <div value>{{data.firstName}} {{data.lastName}}</div>
                    </div>
                    <div class="mb-4">
                                <label class="title">Is Active</label>
                                <div value>{{ data.isActive === 0 ? "No" : "Yes" }}</div>
                               
                         
                      </div>
                    <div class="mb-4">
                        <label class="title">TYPE</label>
                        <div value v-if="data.category === 'personal trainer'">PT</div>
                        <div value v-else-if="data.category === 'class'">Class</div>
                        <div v-else>---</div>
                    </div>
                    <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">PRICE</label>
                        <div value v-if="data.price">{{ data.price | numberFormat }} CREDIT</div>
                        <div v-else>---</div>
                    </div>

                      <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">LOCATION</label>
                        <div value v-if="data.location">{{ data.location.location_name }}</div>
                        <div v-else>---</div>
                    </div>

                     <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">DURATION</label>
                        <div value v-if="data.duration">{{ data.duration | numberFormat }} minutes</div>
                        <div v-else>---</div>
                    </div>
                    
                    <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">LOYALTY POINTS</label>
                        <div value v-if="data.rewardPoints">{{ data.rewardPoints }}</div>
                        <div v-else>---</div>
                    </div>
                  
                    <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">SPECIALIZATION</label>
                        <div value v-if="data.teacherSpecialization">
                          <ul>
                            <li v-for="item in data.teacherSpecialization" :key="item.id">{{ item.teacherSpecialization.name }}</li>
                          </ul>
                        </div>
                        <div v-else>---</div>
                    </div>
                    <div  v-if="data.category === 'personal trainer'" class="mb-4">
                        <label class="title">ABOUT</label>
                        <div value v-if="data.about">{{ data.about }}</div>
                        <div v-else>---</div>
                    </div>
                </div>
            </div>
        </div>

      
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Confirmation from '@/components/Confirmation'

    export default {
        name: 'detail-trainer',
        props: ['id'],
        components: {
            'confirmation': Confirmation
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data(){
            return{
                profile: {},

                is_loading: false,
                is_member: false,
                is_can_edit: false,
                delete_confirmation: false,
                data: {
                    firstName: '',
                    lastName: '',
                    price: '',
                    rewardPoints: '',
                    type: '',
                },
            }
        },
        mounted(){
            this.getAuthUser()
            this.getDetailUser()
        },
        methods: {
            getAuthUser(){
                if(this.isAuthenticated){
                    if(typeof this.user.id == typeof undefined || !this.user.id){
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {})
                    } else{
                        this.profile = this.user
                    }
                }
            },
            getDetailUser(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'teacher/' + this.id).then(response => {
                    this.is_loading = false
                    if(response.status == 404){
                        this.$toastr('error', response.statusText)
                        this.$router.push({ name: 'trainer', params: {} })
                    } else if(response.status == 200){
                        var status = response.data.status
                        if(status){
                            this.data = response.data.data
                        }
                    }
                }, response => {})
            },
           
        }
    }
</script>

<style lang="css" scoped>
    .detail-profile{
        text-align: left;
    }
    .detail-profile label{
        margin-bottom: .1rem;
        font-size: 12px;
        letter-spacing: .75px;
    }
    .detail-profile [value]{
        font-size: 14px;
        font-family: 'gothammedium';
    }
    @media (max-width: 576px) {
        .detail-profile{
            text-align: center;
        }
    }

    @media (max-width: 480px) {
        [action-button]{
            display: block;
        }
    }

    .tab ul{
        padding-left: 0;
        list-style: none;
    }
    .tab ul li{
        display: inline-block;
    }
    .tab ul li{
        padding: 5px 15px;
    }
    .tab ul li a{
        color: #ef3b29;
    }
    .tab ul li.active{
        background-color: #ef3b29;
        border-radius: 25px;
        padding: 5px 15px;
    }
    .tab ul li.active a{
        color: #fff;
    }
    .tab ul li a:not(.active):hover{
        opacity: .7;
    }
    .tab ul li a.active:hover{
        box-shadow: 0 0 3px 1px #ef3b29;
    }
</style>



