<template>
    <div class="">
        <div class="mb-4">
            <label class="page-header">VOUCHER</label>
            <label class="breadcrumb">Voucher List/Create Voucher</label>
        </div>
        <label class="page-title">CREATE VOUCHER</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <form class="mt-3" @submit.prevent="doCreate()">
                    <div class="form-group mb-45">
                        <label>VOUCHER NAME <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.name" required />
                    </div>
                     <div class="form-group mb-45">
                        <label>VOUCHER CODE <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.code" required />
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea type="text" maxlength="30" class="form-control" v-model="form.description" required />
                    </div>

                    <div class="form-group mb-45">
                                    <label class="mb-2">SELECT LOCATION <span class="red">*</span></label>
                                    <multiselect 
                                    v-model="selected_location" 
                                    :options="location_options" 
                                    @search-change="getListLocation" 
                                    :searchable="true" 
                                    track-by="id" label="location_name" :close-on-select="true" :show-labels="false" placeholder="SELECT LOCATION"></multiselect>
                                     
                    </div>
               

                    <div class="row align-items-center">
                        <div class="col-12 col-md-5">
                            <div class="form-group mb-5">
                                <label>EXPIRATION DATE <span class="red">*</span> </label>
                                <div class="input-group front back">
                                    <span class="front icon"> <i class="fa fa-calendar" aria-hidden="true"></i> </span>
                                    <span class="back icon" v-if="form.expired !== ''">
                                        <a href="#deleteDate" @click.prevent="form.expired = ''">
                                            <i class="fa fa-times" aria-hidden="true"></i>
                                        </a>
                                    </span>
                                </div>
                                <datetime v-model="form.expired" format="dd MMMM yyyy" input-class="form-control datetime front" class="theme light" :auto="true" :week-start="7"></datetime>
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-12 col-md-6">
                            <div class="form-group mb-45">
                                <label>VOUCHER TYPE<span class="red">*</span> </label>
                                <div class="mt-2">
                                    <div class="form-check form-check-inline mb-1" v-for="type_option in type_options">
                                        <input class="form-check-input" type="radio" v-model="type" :value="type_option.id" :id="'type-' + type_option.id" />
                                               <label class="form-check-label" :for="'type-' + type_option.id">
                                               {{ type_option.label }}
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="form-group mb-45" >
                        <label class="mb-2">APPLIED TO</label>
                        <multiselect 
                            v-model="form.memberIDs" 
                            :custom-label="labelfullname"
                            placeholder="SELECT MEMBER" 
                            label="firstName" track-by="id" 
                            :options="member_options" 
                            :multiple="true"
                            :loading="isLoading"
                            :searchable="true"
                            :hide-selected="true" 
                            :show-no-results="true" 
                            @search-change="getListUser">
                                <template slot="singleLabel" slot-scope="{ option }">
                                    <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                                   <template slot="option" slot-scope="{ option }">
                                <div>{{ option.firstName }} {{ option.lastName }}</div>
                                </template>
                        </multiselect>
                    </div>

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">CREATE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link :to="{ name: 'reward', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import { Datetime } from 'vue-datetime'
    import Multiselect from 'vue-multiselect'

    export default {
        name: '',
        components: {
            Multiselect,
            'datetime': Datetime
        },
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data() {
            return {
                profile: {},
                member_options: [],
                type: 'class',
                type_options: [
                    {id: 'class', label: 'Class'},
                    {id: 'recovery', label: 'Recovery'}
                   
                ],
                selected_location : null,
                location_options: [],
                is_loading: false,
                isLoading: false,
                last_page : '',
                current_page : '',
                last_page_loc : '',
                current_page_loc : '',
                select_memberIDs : [],
                form: {
                    code : '',
                    expired: '',
                    limit : '',
                    name: '',
                    description: '',
                    memberIDs: [],
                    quota: '',
                    // type: '2',
                    category: "",
                    locationID : ''
                }
            }
        },
        mounted() {
            this.getAuthUser()
            this.getListUser()
            this.getListLocation()
        },
        methods: {
             labelfullname({ firstName, lastName }) {
            return `${firstName} ${lastName}`;
                },
            getAuthUser() {
                if (this.isAuthenticated) {
                    if (typeof this.user.id == typeof undefined || !this.user.id) {
                        this.$http.get(process.env.API_URL + 'user/profile').then(response => {
                            this.profile = response.data.data
                        }, response => {
                        })
                    } else {
                        this.profile = this.user
                    }
                }
            },
               getListUser(query = null, page = null ){
                this.$http.get(process.env.API_URL + 'user/all', {params: {'keyword': query, 'page': page}}).then(response => {
                    var status = response.data.status
                    if(status){
     
                        this.member_options = response.data.data
                    
                    }
                }, response => {})
            },
            getListLocation(query = null){
                this.$http.get(process.env.API_URL + 'location/all', {params: {'keyword': query}}).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.location_options = response.data.data
                    }
                }, response => {})
            },
            onChangeFile(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.form.image = files[0];
            },
            doCreate() {
                var moment = require('moment');
                var formData = new FormData();
                formData.set('expired', moment(this.form.expired).format('YYYY-MM-DD'));
                formData.set('code', this.form.code);
                formData.set('name', this.form.name);
                formData.set('limit', this.form.limit);
                formData.set('description', this.form.description);
                formData.set('quota', this.form.quota);
                formData.set('category', this.type);
                formData.set('locationID', this.selected_location ? this.selected_location.id : '');
                formData.append('memberIDs', `[${this.form.memberIDs.map(e => e.id)}]`)

                this.is_loading = true
            
                    this.$http.post(process.env.API_URL + 'reward-voucher', formData).then(response => {
                        this.is_loading = false
                        var status = response.data.status
                        if (status) {
                            this.$toastr('success', response.data.message)
                            this.$router.push({name: 'reward', params: {}})
                        }
                    }, response => {
                    })
            }
        }
    }
</script>

<style lang="css" scoped>

</style>



