<template>
    <div class="terms">
        <div class="mb-4">
            <label class="page-header">SUBCONTENT {{this.id === 1 ? "CLASS" : "CREDIT"}}</label>
        </div>
        <label class="page-title">EDIT SUBCONTENT</label>
        <div class="row">
            <div class="col-12 col-sm-11 col-md-10 col-lg-8">
                <div class="" v-if="is_loading">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                </div>

                <form v-else class="mt-3" @submit.prevent="doUpdate()">
                   

                    <div class="form-group mb-45">
                        <label>TITLE <span class="red">*</span> </label>
                        <input type="text" class="form-control" v-model="form.title" required>
                    </div>

                    <div class="form-group mb-45">
                        <label>DESCRIPTION <span class="red">*</span> </label>
                        <textarea class="form-control" v-model="form.description" required> </textarea>
                    </div>

                  

                    <div class="mt-5">
                        <button type="submit" class="btn btn-main small mr-2" :disabled="is_loading">
                            <span v-if="!is_loading">SAVE</span>
                            <span class="loading" v-else>
                                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                            </span>
                        </button>
                        <router-link v-if="id === 1" :to="{ name: 'class', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                          <router-link v-if="id === 2" :to="{ name: 'credit_price', params: {} }" class="btn btn-main inverse small">CANCEL</router-link>
                    </div>

                    <div class="loading">

                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
  

    export default {
        name: 'edit_subcontent',
        props: ['id'],
     
        computed: mapState([
            'user',
            'isAuthenticated'
        ]),
        data (){
            return {
                profile: {},
                data:{},
               
                is_loading: false,
                form: {
                    title:'',
                    description:''
                }
            }
        },
        mounted(){
           this.getDetailSubContent()
        },
        methods: {
           
        
            getDetailSubContent(){
                this.is_loading = true
                this.$http.get(process.env.API_URL + 'subcontent/' + this.id).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        this.data = response.data.data
                        this.form.title = this.data.title
                       this.form.description = this.data.description
                       
                    }
                }, response => {})
            },
            doUpdate(){
                this.is_loading = true
             
                this.$http.post(process.env.API_URL + 'subcontent/update/' + this.id, this.form).then(response => {
                    this.is_loading = false
                    var status = response.data.status
                    if(status){
                        if(this.id === 1) {
                            this.$router.push({ name: 'class', params: {} })
                            this.$toastr('success', response.data.message)
                        }else {
                              this.$router.push({ name: 'credit_price', params: {} })
                            this.$toastr('success', response.data.message)
                        }
                      
                    }
                }, response => {})
            },
            
        }
    }
</script>

<style lang="css" scoped>
    .radio-group input[type=radio] {
        position: absolute;
        visibility: hidden;
        display: none;
    }

    .radio-group label {
        color: #ef3b29;
        display: inline-block;
        cursor: pointer;
        font-weight: bold;
        padding: 9px 20px;
        width: 100%;
        border: solid 1px #ef3b29;
    }
    .radio-group label::before{
        content: none;
    }
    .radio-group label::after{
        content: none;
    }

    .radio-group input[type=radio]:checked + label {
        color: #fff;
        background: #ef3b29;
        border: solid 1px #ef3b29;
    }

    .radio-group label + input[type=radio] + label{
        border: solid 1px #ef3b29;
    }

    .radio-group {
        overflow: hidden;
    }

    .terms textarea{
        min-height: 150px;
        display: block;
    }
</style>



